<!--Анимация о компании1-->
<template>
  <div
    class="animate-image8"
    @mouseover="zoomImg"
    @mouseout="returnImg"
  >
    <div
      ref="image"
      class="animate-image8__img"
    >
      <ConvertImage
        :image="image"
        :kind="kind"
      />
    </div>
    <div class="animate-image8__bg-svg">
      <Type8 />
    </div>
    <div class="animate-image8__bg-svg2">
      <Type8Second />
    </div>
    <div class="animate-image8__circle-top1">
      <CircleTop1 />
    </div>
    <div class="animate-image8__circle-top2">
      <CircleTop2 />
    </div>
    <div class="animate-image8__circle-top3">
      <CircleTop3 />
    </div>
    <div class="animate-image8__circle-bottom1">
      <CircleBottom1 />
    </div>
    <div class="animate-image8__circle-bottom2">
      <CircleBottom2 />
    </div>
  </div>
</template>

<script>
import Type8 from '@icons/AnimateImage/type8/type8.svg';
import Type8Second from '@icons/AnimateImage/type8/type8_2.svg';
import CircleTop1 from '@icons/AnimateImage/type8/circleTop1.svg';
import CircleTop2 from '@icons/AnimateImage/type8/circleTop2.svg';
import CircleTop3 from '@icons/AnimateImage/type8/circleTop3.svg';
import CircleBottom1 from '@icons/AnimateImage/type8/circleBottom.svg';
import CircleBottom2 from '@icons/AnimateImage/type8/circleBottom2.svg';
import ConvertImage from '@components/ConvertImage/ConvertImage';
export default {
  name: 'AnimateImage8',
  components:{
    Type8,
    Type8Second,
    CircleTop1,
    CircleTop2,
    CircleTop3,
    CircleBottom1,
    CircleBottom2,
    ConvertImage,
  },
  props: {
    image: {
      type: String,
      default: '',
    },
    kind:{
      type: [ Number, String ],
      default: 1,
    },
  },
  methods:{
    zoomImg(){
      let image = this.$refs.image.querySelector('image');
      image.style.transform = 'scale(1.1) translate(-36px, 0px)';
    },
    returnImg(){
      let image = this.$refs.image.querySelector('image');
      image.style.transform = 'inherit';
    },
  },
};
</script>

<style lang="scss" src="./AnimateImage8.scss" scoped>

</style>