<template>
  <div class="animate-brand">
    <div class="animate-brand__bg">
      <img
        :src="getImg(lightBg)"
        alt="light-bg"
      >
    </div>
    <div class="animate-brand__dark-bg">
      <img
        :src="getImg(darkBg)"
        alt="dark-bg"
      >
    </div>
    <div class="animate-brand__image">
      <img
        :src="getImg(image)"
        alt="photo"
      >
    </div>
  </div>
</template>

<script>

export default {
  name: 'AnimateBrand',
  props:{
    image:{
      type:String,
      default:'',
    },
    lightBg:{
      type:String,
      default:'',
    },
    darkBg:{
      type:String,
      default:'',
    },
  },
  methods:{
    getImg(image) {
      return require(`@images/AnimateBrand/${image}`);
    },
  },
};
</script>

<style lang="scss" src="./AnimateBrand.scss" scoped>

</style>
