<template>
  <SwiperSlide>
    <div
      class="brand-slide"
    >
      <div class="brand-slide__left">
        <h1>
          {{ title }}
          <slot name="title" />
        </h1>
        <h2>
          {{ subtitle }}
        </h2>
        <p>{{ text }}</p>
        <div class="brand-slide__actions">
          <slot />
        </div>
      </div>
      <div class="brand-slide__right">
        <picture class="brand-slide__background">
          <source
            v-if="wide"
            :srcset="getImage(`${background}-wide`)"
            media="(min-width: 1921px)"
          >
          <source
            :srcset="getImage(`${background}-mobile`)"
            media="(max-width: 779px)"
          >
          <img
            :src="getImage(background)"
            alt="background-1"
          >
        </picture>
        <img
          class="brand-slide__pack"
          :src="getImage(pack)"
          alt="pack-1"
        >
      </div>
    </div>
  </SwiperSlide>
</template>

<script>
import { SwiperSlide } from 'vue-awesome-swiper';
export default {
  name: 'BrandSlide',
  components:{
    SwiperSlide,
  },
  props:{
    background:{
      type:String,
      default:() => '',
    },
    pack:{
      type:String,
      default:() => '',
    },
    title:{
      type:String,
      default:() => '',
    },
    subtitle:{
      type:String,
      default:() => '',
    },
    text:{
      type:String,
      default:() => '',
    },
    wide:{
      type:Boolean,
      default:()=>false,
    },
  },
  methods:{
    getImage(image){
      return require(`@images/BrandSlider/${image}.png`);
    },
  },
};
</script>

<style lang="scss" src="./BrandSlide.scss" scoped/>
