export const productModule = {
  state: {
    tabsList: [
      {
        name: 'Общее описание',
        value: 'description',
      },
      {
        name: 'Состав',
        value: 'structure',
      },
      {
        name: 'Инструкция',
        value: 'instruction',
      },
      {
        name: 'Отзывы',
        anchor:'#reviews-slider',
      },
      {
        name: 'Где купить?',
        anchor:'#where-buy',
      },
    ],
  },
  getters: {
    tabsList: (state) => state.tabsList,
  },
  mutations: {
    updateTabsList: (state, payload) => {
      state.tabsList = payload;
    },

  },
  actions: {
    setTabsList({ commit }, query) {
      commit('updateTabsList', query);
    },
  },
};
