<template>
  <div class="article-card">
    <div class="article-card__image">
      <AnimateImage6
        v-if="kind==6"
        image="type6.jpg"
        kind="6"
      />
      <AnimateImage3
        v-if="kind==3"
        image="type3.jpg"
        kind="3"
      />
    </div>

    <h3 class="article-card__title">
      {{ title }}
    </h3>
    <p class="article-card__desc">
      {{ desc }}
    </p>
    <UiLink
      class="article-card__link"
      to="/articles/1"
    >
      <span>
        Подробнее
      </span>
      <ArrowRight />
    </UiLink>
  </div>
</template>

<script>
import AnimateImage6 from '@components/AnimateImages/AnimateImage6/AnimateImage6';
import AnimateImage3 from '@components/AnimateImages/AnimateImage3/AnimateImage3';
import UiLink from '@ui/UiLink/UiLink';
import ArrowRight from '@icons/ArticleCard/arrow.svg';
export default {
  name: 'ArticleCard',
  components: {
    UiLink,
    AnimateImage6,
    AnimateImage3,
    ArrowRight,
  },
  props:{
    href:{
      type:String,
      default:()=>'',
    },
    title:{
      type:String,
      default:()=>'',
    },
    desc:{
      type:String,
      default:()=>'',
    },
    kind:{
      type:[ String, Number ],
      default:()=>'6',
    },
    id:{
      type: Number,
      default: 0,
    },
  },
};
</script>

<style lang="scss" src="./ArticleCard.scss" scoped/>
