<template>
  <teleport
    to="body"
  >
    <div
      class="popup-right"
      :class="classes"
      @click="$emit('input',false)"
    >
      <div
        class="popup-right__inner"
        @click.stop
      >
        <CloseButton
          class="popup-right__close-button"
          square
          @click="$emit('input',false)"
        />
        <slot />
      </div>
    </div>
  </teleport>
</template>

<script>
import CloseButton from '@components/Buttons/CloseButton/CloseButton';
import teleport from 'vue2-teleport';
export default {
  name: 'PopupRight',
  components: {
    CloseButton,
    teleport, 
  },
  props: {
    value: {
      type: Boolean,
      default: () => false,
    },
    mobileModal: {
      type: Boolean,
      default: () => false,
    },
    white: {
      type: Boolean,
      default: () => false,
    },
    filter: {
      type: Boolean,
      default: () => false,
    },
  },
  computed:{
    classes(){
      return {
        'popup-right--active':this.value,
        'popup-right--white':this.white,
        'popup-right--filter':this.filter,
      };
    },
  },
  watch: {
    value(value) {
      if (value ) document.documentElement.style.overflowY = 'hidden';
      else if(!this.mobileModal) document.documentElement.style.overflowY = 'inherit';
    },
  },

};
</script>

<style lang="scss" src="./PopupRight.scss" scoped/>
