<template>
  <div class="card-slider">
    <Swiper
      :options="swiperOptions"
    >
      <slot />
    </Swiper>
    <div
      class="card-slider__pagination"
      :class="'card-slider__pagination-'+id"
    />
  </div>
</template>

<script>
import { Swiper } from 'vue-awesome-swiper';
export default {
  name: 'CardSlider',
  components: {
    Swiper,
  },
  props:{
    id:{
      type:[ Number, String ],
      required:true,
    },
  },

  data() {
    return {
      swiperOptions: {
        pagination: {
          el: '.card-slider__pagination-'+this.id,
        },
      },
    };
  },

};
</script>

<style lang="scss" src="./CardSlider.scss" scoped/>
