<template>
  <label class="ui-checkbox">
    <input
      v-model="checked"
      type="checkbox"
      :value="modelValue"
    >
    <span class="ui-checkbox__square">
      <SuccessCheck />
    </span>
  </label>
</template>
<script>
import SuccessCheck from '@icons/UiComponent/TextInput/SuccessCheck.svg';

export default {
  components: {
    SuccessCheck, 
  },
  props:{
    modelValue:{
      type: [ String, Number, Boolean, Object ],
      default: () => '',
    },
    value:{
      type:[ Array, Boolean ],
      default: () => [],
    },
  },
  computed:{
    checked:{
      get(){
        return this.value;
      },
      set(value){
        this.$emit('input', value);
      },
    },
  },
};
</script>
<style lang="scss" src="./UiCheckbox.scss" scoped/>
 