<template>
  <div class="app-footer-bottom">
    <ContainerTemplate>
      <div class="app-footer-bottom__logo">
        <Amado />
      </div>
      <div class="app-footer-bottom__text">
        <picture>
          <source
            :srcset="getImage('text-phone.png')"
            media="(max-width: 779px)"
          >
          <source
            :srcset="getImage('text-tablet.png')"
            media="(max-width: 1023px)"
          >
          <source
            :srcset="getImage('text-laptop.png')"
            media="(max-width: 1279px)"
          >
          <source
            :srcset="getImage('text-desktop.png')"
            media="(max-width: 1599px)"
          >
          <img
            :src="getImage('text.png')"
            alt=""
          >
        </picture>
      </div>
    </ContainerTemplate>
  </div>
</template>

<script>
import ContainerTemplate from '@templates/ContainerTemplate/ContainerTemplate';
import Amado from '@icons/AppFooter/AppFooterBottom/amado.svg';

export default {
  name: 'AppFooterBottom',
  components: {
    ContainerTemplate,
    Amado,
  },
  methods:{
    getImage(image){
      return require(`@images/AppFooter/AppFooterBottom/${image}`);
    },
  },
};
</script>

<style lang="scss" src="./AppFooterBottom.scss" scoped/>