<template>
  <div
    class="product-card"
    :class="{'product-card--favorite':fav,'product-card--catalog':catalog}"
  >
    <div class="product-card__inner">
      <BadgeList class="product-card__badges">
        <BadgeItem
          v-for="(badge,index) in badges"
          :key="index"
          :background="badge.color"
        >
          {{ badge.name }}
        </BadgeItem>
      </BadgeList>
      <LikeCheckbox
        v-model="likeModel"
        class="product-card__like"
      />
      <div class="product-card__photo-slider">
        <CardSlider
          v-if="images.length"
          :id="id"
        >
          <CardSlide
            v-for="(image,index) in images"
            :key="index"
          >
            <img :src="getImage(image)">
          </CardSlide>
        </CardSlider>
      </div>
      <div class="product-card__info">
        <div
          v-if="!catalog"
          class="product-card__info-firm"
        >
          {{ firm }}
        </div>
        <h4 class="product-card__info-title">
          {{ title }}
        </h4>
        <div
          v-if="catalog"
          class="product-card__info-firm product-card__info-firm--tablet"
        >
          <span>{{ tabletDescLeft }}</span>
          <span>{{ tabletDescRight }}</span>
        </div>

        <p class="product-card__info-desc">
          {{ desc }}
        </p>
        <div
          v-if="catalog"
          class="product-card__ingredients"
        >
          <IngredientCard img="ingredient.jpg">
            Боярышник
          </IngredientCard>
          <IngredientCard img="ingredient.jpg">
            Боярышник
          </IngredientCard>
          <IngredientCard img="ingredient.jpg">
            Боярышник
          </IngredientCard>
        </div>

        <UiButton
          class="product-card__info-more"
          :to="link"
          transparent
        >
          Подробнее
        </UiButton>
      </div>
    </div>
  </div>
</template>

<script>
import UiButton from '@ui/UiButton/UiButton';
import CardSlider from '@components/CardSlider/CardSlider';
import CardSlide from '@components/CardSlider/CardSlide/CardSlide';
import BadgeList from '@components/BadgeList/BadgeList';
import BadgeItem from '@components/BadgeList/BadgeItem/BadgeItem';
import LikeCheckbox from '@components/LikeCheckbox/LikeCheckbox';
import IngredientCard from '@components/IngredientCard/IngredientCard';

export default {
  name: 'ProductCard',
  components: {
    IngredientCard,
    LikeCheckbox,
    BadgeItem,
    BadgeList,
    CardSlide,
    CardSlider,
    UiButton,
  },
  props: {
    id: {
      type: [ Number, String ],
      required: true,
    },
    firm: {
      type: String,
      default: () => '',
    },
    title: {
      type: String,
      default: () => '',
    },
    desc: {
      type: String,
      default: () => '',
    },
    badges: {
      type: Array,
      default: () => [],
    },
    images: {
      type: Array,
      default: () => [],
    },
    tabletDescLeft: {
      type: [ String, Number ],
      default: () => 0,
    },
    tabletDescRight: {
      type: [ String, Number ],
      default: () => 0,
    },
    fav: {
      type: Boolean,
      default: () => false,
    },
    catalog: {
      type: Boolean,
      default: () => false,
    },
    link: {
      type: String,
      default: () => '',
    },
  },
  data() {
    return {
      likeModel: false, 
    };
  },
  methods: {
    getImage(image) {
      return require('@icons/CardSlider/' + image);
    },
  },
};
</script>

<style lang="scss" src="./ProductCard.scss" scoped/>
