<!--Анимация о компании2-->
<template>
  <div
    class="animate-image9"
    @mouseover="zoomImg"
    @mouseout="returnImg"
  >
    <div
      ref="image"
      class="animate-image9__img"
    >
      <ConvertImage
        :image="image"
        :kind="kind"
      />
    </div>
    <div class="animate-image9__bg-svg">
      <Type9 />
    </div>
    <div class="animate-image9__bg-svg2">
      <Type9Svg />
    </div>
    <div class="animate-image9__circle-top1">
      <CircleTop1 />
    </div>
    <div class="animate-image9__circle-top2">
      <CircleTop2 />
    </div>
    <div class="animate-image9__circle-bottom1">
      <CircleBottom1 />
    </div>
    <div class="animate-image9__circle-bottom2">
      <CircleBottom2 />
    </div>
    <div class="animate-image9__circle-bottom3">
      <CircleBottom3 />
    </div>
  </div>
</template>

<script>
import Type9 from '@icons/AnimateImage/type9/type9.svg';
import Type9Svg from '@icons/AnimateImage/type9/type9_svg2.svg';
import CircleTop1 from '@icons/AnimateImage/type9/circleTop1.svg';
import CircleTop2 from '@icons/AnimateImage/type9/circleTop2.svg';
import CircleBottom1 from '@icons/AnimateImage/type9/circleBottom1.svg';
import CircleBottom2 from '@icons/AnimateImage/type9/circleBottom2.svg';
import CircleBottom3 from '@icons/AnimateImage/type9/circleBottom3.svg';
import ConvertImage from '@components/ConvertImage/ConvertImage';
export default {
  name: 'AnimateImage9',
  components:{
    Type9,
    Type9Svg,
    CircleTop1,
    CircleTop2,
    CircleBottom1,
    CircleBottom2,
    CircleBottom3,
    ConvertImage,
  },
  props: {
    image: {
      type: String,
      default: '',
    },
    kind:{
      type: [ Number, String ],
      default: 1,
    },
  },
  methods:{
    zoomImg(){

      let image = this.$refs.image.querySelector('image');
      image.style.transform = 'scale(1.05)';
    },
    returnImg(){
      let image = this.$refs.image.querySelector('image');
      image.style.transform = 'scale(1.01)';
    },
  },
};
</script>

<style lang="scss" src="./AnimateImage9.scss" scoped>

</style>