<template>
  <div
    class="search-input"
    :class="searchClasses"
  >
    <TextInput
      class="search-input__input"
      :value="value"
      :placeholder="placeholder"
      @input="(val) => $emit('input',val)"
      @focus="focusSearch"
      @blur="blurSearch"
    />
    <span
      v-if="searchActivePlaceholder"
      class="search-input__placeholder"
    >
      {{ activePlaceholder || placeholder }}
    </span>

    <SearchIcon class="search-input__search" />
    <button
      class="search-input__close"
      @click="setValue()"
    >
      <CloseIcon />
    </button>
    <transition name="fade">
      <ul v-if="searchActive">
        <li
          v-for="hint in searchHints"
          :key="hint.value"
        >
          <a
            @click="setValue(hint.name)"
          >
            {{ hint.name }}
          </a>
        </li>
        <li>
          <a
            @click="seeAll()"
          >

            все результаты
            <ArrowIcon />
          </a>
        </li>
      </ul>
    </transition>
  </div>
</template>

<script>
import TextInput from '@ui/TextInput/TextInput';
import SearchIcon from '@icons/SearchInput/search.svg';
import CloseIcon from '@icons/SearchInput/close.svg';
import ArrowIcon from '@icons/SearchInput/arrow.svg';
export default {
  name: 'SearchInput',
  components: {
    TextInput,
    SearchIcon,
    CloseIcon,
    ArrowIcon, 
  },
  props: {
    mapSearch:{
      type: Boolean,
      default:()=>false,
    },
    value: {
      type: String,
      default: () => '',
    },
    placeholder: {
      type: String,
      default: () => '',
    },
    activePlaceholder: {
      type: String,
      default: () => '',
    },
    searchHints:{
      type:Array,
      default:()=>[],
    },
  },
  data(){
    return {
      searchFocus:false,
    };
  },
  computed:{
    searchActive(){
      return this.searchFocus || this.value.length;
    },
    searchActivePlaceholder() {
      return this.searchFocus && !this.value.length;
    },
    searchClasses(){
      return {
        'search-input--active':this.searchActive,
        'search-input--map':this.mapSearch,
      };
    },
  },
  methods:{
    seeAll(){
      this.$emit('seeAll');
    },
    setValue(value = ''){
      this.$emit('input', value);
    },
    focusSearch(){
      this.searchFocus = true;
    },
    blurSearch(){
      this.searchFocus = false;
    },
  },
};
</script>

<style lang="scss" src="./SearchInput.scss" scoped/>