<template>
  <UiLink
    :to="`/stocks/${id}`"
    class="animate-banner"
    :class="'animate-banner--'+kind"
    :style="`background:${background}`"
  >
    <div class="animate-banner__dark-block" />

    <UiLink
      class="animate-banner__link"
      :to="`/stocks/${id}`"
      white
    >
      Подробнее
      <SimpleArrowButton
        next-btn
        white
      />
    </UiLink>
    <img
      v-if="image"
      class="animate-banner__image"
      :src="require(`@images/AnimateBanner/${image}`)"
      alt="photo"
    >
    <img
      v-if="mobileImage"
      class="animate-banner__image animate-banner__image--mobile"
      :src="require(`@images/AnimateBanner/${mobileImage}`)"
      alt="photo"
    >
    <!-- eslint-disable vue/no-v-html -->
    <div class="animate-banner__text">
      <h2
        v-if="title"
        :style="colorText && `color:${colorText}`"
        v-html="title"
      />
      <h1
        v-if="price"
        :style="colorText && `color:${colorText}`"
        v-html="price"
      />
      <div
        v-if="subtitle"
        class="animate-banner__subtitle"
        :style="colorText && `color:${colorText}`"
        v-html="subtitle"
      />
    </div>
    <!-- eslint-enable -->
  </UiLink>
</template>

<script>
import SimpleArrowButton from '@components/Buttons/SimpleArrowButton/SimpleArrowButton';
import UiLink from '@ui/UiLink/UiLink';
export default {
  name: 'AnimateBanner',
  components:{
    UiLink,
    SimpleArrowButton,
  },
  props:{
    background:{
      type:String,
      default:'#46C0C1',
    },
    image:{
      type:String,
      default:'',
    },
    mobileImage:{
      type:String,
      default:'',
    },
    title:{
      type:String,
      default:'',
    },
    subtitle:{
      type:String,
      default:'',
    },
    price:{
      type:String,
      default:'',
    },
    kind:{
      type:[ String, Number ],
      default:1,
    },
    colorText:{
      type:String,
      default:'',
    },
    id:{
      type:Number,
      default:1,
    },
  },


};
</script>

<style lang="scss" src="./AnimateBanner.scss" scoped>

</style>
