<template>
  <div class="animate-plus-blue">
    <Background />
    <img
      :src="getImg(image)"
      alt="image"
      class="animate-plus-blue__image"
    >
    <div class="animate-plus-blue__text">
      {{ text }}
    </div>
    <div class="animate-plus-blue__hover-text">
      {{ hoverText }}
    </div>
    <div class="animate-plus-blue__circle-top1">
      <CircleTop1 />
    </div>
    <div class="animate-plus-blue__circle-top2">
      <CircleTop2 />
    </div>
    <div class="animate-plus-blue__circle-bottom">
      <CircleBottom1 />
    </div>
  </div>
</template>

<script>
import Background from '@icons/AnimatePlus/blue/bg-svg.svg';
import CircleTop1 from '@icons/AnimatePlus/blue/circleTop1.svg';
import CircleTop2 from '@icons/AnimatePlus/blue/circleTop2.svg';
import CircleBottom1 from '@icons/AnimatePlus/blue/circleBottom1.svg';
export default {
  name: 'AnimatePlusBlue',
  components: {
    Background,
    CircleTop1,
    CircleTop2,
    CircleBottom1,
  },
  props:{
    image:{
      type:String,
      default:'',
    },
    text:{
      type: String,
      default: '',
    },
    hoverText:{
      type: String,
      default: '',
    },

  },
  methods:{
    getImg(image) {
      return require(`@images/AnimatePlus/${image}`);
    },
  },

};
</script>

<style lang="scss" src="./AnimatePlusBlue.scss" scoped>

</style>
