<template>
  <div
    class="sort-list"
    :class="{'sort-list--big':isBig}"
  >
    <span>
      <slot />
    </span>
    <div
      class="sort-list__dropdown"
      :class="{'sort-list__dropdown--active':optionsVisible}"
    >
      <button @click="optionsVisible=!optionsVisible">
        {{ value.name }}
        <ListArrow />
      </button>
      <transition name="fade">
        <ul
          v-if="optionsVisible"
        >
          <li
            v-for="option in options"
            :key="option.value"
          >
            <a
              @click="selectOption(option)"
            >
              {{ option.name }}
            </a>
          </li>
        </ul>
      </transition>
    </div>
  </div>
</template>

<script>
import ListArrow from '@icons/SortList/ListArrow.svg';

export default {
  name: 'SortList',
  components: {
    ListArrow, 
  },
  props: {
    isBig:{
      type:Boolean,
      default:()=>false,
    },
    value:{
      type:Object,
      default:()=>({}),
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      optionsVisible: false,
    };
  },
  methods: {
    selectOption(option) {
      this.$emit('input', option);
      this.optionsVisible = false;
    },
  },
};
</script>

<style lang="scss" src="./SortList.scss" scoped/>