<template>
  <button
    class="square-arrow-button"
    :class="classes"
    @click="$emit('click')"
  >
    <CornerArrowIcon />
  </button>
</template>

<script>
import CornerArrowIcon from '@icons/UiComponent/UiButton/CornerArrowIcon.svg';

export default {
  name: 'SquareArrowButton',
  components:{
    CornerArrowIcon,
  },
  props:{
    right:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    classes(){
      return {
        'square-arrow-button--right':this.right,
      };
    },
  },

};
</script>

<style lang="scss" src="./SquareArrowButton.scss" scoped/>