<template>
  <div class="animate-plus-light-blue">
    <Background />
    <img
      :src="getImg(image)"
      alt="image"
      class="animate-plus-light-blue__image"
    >
    <div class="animate-plus-light-blue__text">
      {{ text }}
    </div>
    <div class="animate-plus-light-blue__hover-text">
      {{ hoverText }}
    </div>
    <div class="animate-plus-light-blue__circle-top1">
      <CircleTop1 />
    </div>
    <div class="animate-plus-light-blue__circle-top2">
      <CircleTop2 />
    </div>
  </div>
</template>

<script>
import Background from '@icons/AnimatePlus/light-blue/bg-svg.svg';
import CircleTop1 from '@icons/AnimatePlus/light-blue/circleTop1.svg';
import CircleTop2  from '@icons/AnimatePlus/light-blue/circleTop2.svg';
export default {
  name: 'AnimatePlusLightBlue',
  components: {
    Background,
    CircleTop1,
    CircleTop2,
  },
  props:{
    image:{
      type:String,
      default:'',
    },
    text:{
      type: String,
      default: '',
    },
    hoverText:{
      type: String,
      default: '',
    },

  },
  methods:{
    getImg(image) {
      return require(`@images/AnimatePlus/${image}`);
    },
  },

};
</script>

<style lang="scss" src="./AnimatePlusLightBlue.scss" scoped>

</style>
