<template>
  <teleport to="body">
    <div
      ref="popupGallery"
      class="popup-gallery"
      :class="classesPopup"
    >
      <UiButton
        v-if="actualTypeSlide === 'image'"
        ref="zoomButton"
        class="ui-button--circle ui-button--zoom popup-gallery__zoom"
        text="увеличить"
        @click="zoomImg"
      >
        <ZoomIcon />
        <div class="ui-button__background-zoom" />
      </UiButton>

      <CloseButton
        class="popup-gallery__close"
        square
        @click="closePopup"
      />
      <div class="popup-gallery__content">
        <div
          v-if="gallery.length > 1"
          ref="navigation"
          class="popup-gallery__navigation"
        >
          <SquareArrowButton
            @click="hideVideo"
          />
          <SquareArrowButton
            right
            @click="hideVideo"
          />
          <div
            ref="pagination"
            class="popup-gallery__pagination"
          >
            <GalleryIcon />
            <div class="popup-gallery__numbering">
              {{ actualSlide }} / {{ gallery.length }}
            </div>
          </div>
        </div>
        <Swiper
          ref="mySwiper"
          :options="swiperOption"
          class="popup-gallery__slider"
          @slideChange="updateSlide"
          @click="zoomImg"
        >
          <swiper-slide
            v-for="(file, index) of gallery"
            ref="slide"
            :key="index"
            :data-type="file.type"
            class="popup-gallery__slide"
          >
            <div
              ref="image"
              class="swiper-zoom-container popup-gallery__img"
              data-swiper-zoom="3"
            >
              <div
                class="popup-gallery__img-shadow"
              />
              <img
                data-type="image"
                :src="getImg(file.content.image)"
                alt="photo"
              >
            </div>
            <UiButton
              v-if="actualTypeSlide === 'video'"
              class="ui-button--circle
          ui-button--play popup-gallery__play"
              text="смотреть"
              @click="playVideo"
            >
              <PlayIcon />
              <div class="ui-button__background-play" />
            </UiButton>
            <div
              v-if="file.type === 'video' && !file.content.iframe"
              class="popup-gallery__video-wrp"
            >
              <video
                ref="video"
                controls
                class="popup-gallery__video"
              >
                <source
                  :src="getVideo(file.content.video)"
                  type="video/mp4"
                >
              </video>
            </div>
            <!-- eslint-disable vue/no-v-html -->
            <div
              v-if="file.content.iframe"
              class="popup-gallery__iframe-wrp"
              v-html="file.content.iframe"
            />
            <div />
            <!-- eslint-enable -->

            <div
              class="popup-gallery__slide-data"
            >
              {{ file.content.info }}
            </div>
          </swiper-slide>
        </Swiper>
      </div>
    </div>
  </teleport>
</template>

<script>
import PlayIcon from '@icons/UiComponent/UiButton/PlayIcon.svg';
import UiButton from '@ui/UiButton/UiButton';
import ZoomIcon from '@icons/UiComponent/UiButton/ZoomIcon.svg';
import GalleryIcon from '@icons/PopupGallery/gallery.svg';

import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import teleport from 'vue2-teleport';
import CloseButton from '@components/Buttons/CloseButton/CloseButton';
import SquareArrowButton from '@components/SquareArrowButton/SquareArrowButton';
export default {
  name: 'PopupGallery',
  components:{
    SquareArrowButton,
    teleport,
    CloseButton,
    Swiper,
    SwiperSlide,
    GalleryIcon,
    UiButton,
    ZoomIcon,
    PlayIcon,
  },
  props:{
    gallery:{
      type:Array,
      default:() => [],
    },
    activeIndex:{
      type: Number,
      default:0,
    },
  },
  data(){
    return{
      close: false,
      cursorBlock:true,
      actualSlide:1,
      actualTypeSlide: 'image',
      activeSlide:null,
      allowTouchMove: false,
      mouseOver:false,
      videoPlayed:false,
      swiperOption: {
        initialSlide:0,
        loop: this.gallery.length > 1,
        slidesPerView: 1,
        spaceBetween: 100,
        navigation: {
          nextEl: '.square-arrow-button',
          prevEl: '.square-arrow-button--right',
        },
      },
    };
  },
  computed:{
    classesPopup(){
      return {
        'popup-gallery--mouseover':this.mouseOver,
        'popup-gallery--video-played': this.videoPlayed,
      };
    },
  },
  created() {
    this.swiperOption.initialSlide = this.activeIndex;
    if (this.gallery.length > 1) {
      this.actualSlide = this.activeIndex + 1;
    } else {
      this.actualSlide = 0;
    }
  },
  mounted() {
    this.$refs.mySwiper.$swiper.update();
    this.actualTypeSlide = this.gallery[0].type;
  },
  methods: {
    addActualTypeSlide() {
      let slides = document.querySelectorAll('.popup-gallery__slide.swiper-slide');
      this.activeSlide= [ ...slides ].find(el =>Number(el.dataset.swiperSlideIndex)+1 === this.actualSlide);
      this.actualTypeSlide = this.activeSlide.dataset.type;
    },
    updateSlide(){
      setTimeout(() => {
        this.actualSlide = this.$refs.mySwiper.$swiper.realIndex + 1;
        this.addActualTypeSlide();
      }, 10);
    },
    closePopup() {
      this.close = true;
      this.$emit('closePopup');
      document.documentElement.style.overflow = 'inherit';
    },
    getImg(image) {
      return require(`@images/${image}`);
    },
    getVideo(video) {
      return require(`@video/${video}`);
    },
    zoomImg(){
      let zoomImg = document.querySelector('.swiper-slide-zoomed');
      if (!zoomImg && this.actualTypeSlide === 'image'){
        this.$refs.mySwiper.$swiper.zoom.enable();
        this.$refs.mySwiper.$swiper.zoom.in();
        this.$refs.zoomButton.$el.style.display = 'none';
      } else {
        if (this.$refs.zoomButton){
          this.$refs.mySwiper.$swiper.zoom.out();
          this.$refs.zoomButton.$el.style.display = 'flex';
        }
      }
    },

    hideVideo(){
      console.log('!', this.actualSlide);
      let slides = document.querySelectorAll('.popup-gallery__slide.swiper-slide');
      let image = slides[this.actualSlide].querySelector('.popup-gallery__img');
      image.style.display = 'flex';
      this.videoPlayed = false;


      let playButton = slides[this.actualSlide].querySelector('.popup-gallery__play');
      if (playButton){
        playButton.style.display = 'flex';
      }

      let video = slides[this.actualSlide].querySelector('video');
      if (video){
        video.pause();
        video.currentTime = 0;
      }

      let iframe = slides[this.actualSlide].querySelector('iframe');
      if (iframe && this.videoPlayed){
        let oldSrc = iframe.getAttribute('src');
        let newSrc = oldSrc.replace('autoplay=1', 'autoplay=-1');
        iframe.setAttribute('src', newSrc);
        iframe.stop();
        iframe.currentTime = 0;
      }

    },

    playVideo(){
      console.log('2', this.actualSlide);
      let slides = document.querySelectorAll('.popup-gallery__slide.swiper-slide');
      let image = slides[this.actualSlide].querySelector('.popup-gallery__img');
      image.style.display = 'none';
      let playButton = slides[this.actualSlide].querySelector('.popup-gallery__play');
      playButton.style.display = 'none';

      let video = slides[this.actualSlide].querySelector('video');
      if (video){
        video.play();
      }

      let iframe = slides[this.actualSlide].querySelector('iframe');
      if (iframe){
        let oldSrc = iframe.getAttribute('src');
        let newSrc;
        if (oldSrc){
          newSrc = oldSrc;
          if (oldSrc.indexOf('autoplay') !== -1){
            newSrc = oldSrc.replace('autoplay=0', 'autoplay=1');
            iframe.setAttribute('src', newSrc);
          } else {
            if (oldSrc.indexOf('?') === -1){
              newSrc = newSrc + '?';//включаем видео
            } else {
              newSrc = newSrc + '&';//включаем видео
            }
            newSrc = newSrc + 'autoplay=1';//включаем видео
            iframe.setAttribute('src', newSrc);
            this.videoPlayed = true;
            return newSrc;
          }
        }
      }
      this.videoPlayed = true;

    },
  },
};
</script>

<style lang="scss" src="./PopupGallery.scss" scoped/>
