<template>
  <div class="main-switcher">
    <div class="main-switcher__inner">
      <label
        class="main-switcher__label main-switcher__label--left"
        :class="{'main-switcher__label--active':switchValue === value1}"
      >
        <template v-if="isMap">
          <span class="main-switcher__label-text">
            Списком
          </span>
        </template>
        <template v-else>
          <span class="main-switcher__label-text main-switcher__label-text--desktop">
            Популярные товары
          </span>
          <span class="main-switcher__label-text main-switcher__label-text--mobile">
            Популярное
          </span>
        </template>
        <input
          v-model="switchValue"
          type="radio"
          :value="value1"
        >
      </label>
      <div
        class="main-switcher__switcher"
        :class="{'main-switcher__switcher--active':switchValue === value2}"
      />
      <label
        class="main-switcher__label main-switcher__label--right"
        :class="{'main-switcher__label--active':switchValue === value2}"
      >
        <template v-if="isMap">
          На карте
        </template>
        <template v-else>
          Выбор покупателя
        </template>
        <input
          v-model="switchValue"
          type="radio"
          :value="value2"
        >
      </label>
    </div>
  </div>
</template>
<script>
export default {
  name: 'MainSwitcher',
  props: {
    isMap: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      switchValue: '',
    };
  },
  computed:{
    value1(){
      return this.isMap ? 'list' : 'popular';
    },
    value2(){
      return this.isMap ? 'map' : 'choice';
    },
  },
  watch: {
    switchValue(newValue) {
      this.$emit('input', newValue);
    },
  },
  mounted() {
    this.switchValue = this.value1;
  },
};
</script>
<style lang="scss" src="./MainSwitcher.scss" scoped/>
