<template>
  <div class="catalog-modal-component">
    <LettersList
      v-model="lettersModel"
      class="catalog-modal-component__letters"
    />
    <ScrollBlock>
      <div class="catalog-modal-component__inner catalog-modal-component__inner--desktop">
        <div
          v-for="component in componentsListShuffled"
          :key="component.letter"
          class="catalog-component catalog-modal-component__item"
        >
          <div class="catalog-component__letter">
            {{ component.letter }}
          </div>
          <div class="catalog-component__letter-items">
            <ul>
              <li
                v-for="(item,i) in component.items"
                :key="item + component.letter + i"
              >
                <a href="#">{{ item }}</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="catalog-modal-component__inner catalog-modal-component__inner--mobile">
        <div
          v-for="component in componentsList"
          :key="component.letter"
          class="catalog-component catalog-modal-component__item"
        >
          <div class="catalog-component__letter">
            {{ component.letter }}
          </div>
          <div class="catalog-component__letter-items">
            <ul>
              <li
                v-for="(item,i) in component.items"
                :key="item + component.letter + i"
              >
                <a href="#">{{ item }}</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </ScrollBlock>
  </div>
</template>

<script>
import ScrollBlock from '@components/ScrollBlock/ScrollBlock';
import LettersList from '@components/LettersList/LettersList';

export default {
  name: 'CatalogModalComponent',
  components: {
    LettersList,
    ScrollBlock, 
  },
  data() {
    return {
      lettersModel:'',
      componentsList: [],
      componentsListShuffled: [],
      lettersArray:[ 'а', 'б', 'в', 'г', 'д', 'е/ё', 'ж', 'з', 'и/й', 'к', 'л', 'м', 'н', 'о', 'п', 'р', 'с', 'т', 'у', 'ф', 'х', 'ц', 'ч', 'ш', 'щ', 'э', 'ю', 'я' ],
    };
  },
  mounted() {
    this.createArray();
    this.shuffleArray();
  },
  methods: {
    createArray() {
      for (let i = 0; i < this.lettersArray.length; i++) {
        const newItem = {
          letter: this.lettersArray[i],
          items: [],
        };
        for (let j = 0; j < 10; j++) {
          newItem.items.push('Альфа-липоевая кислота');
        }

        this.componentsList.push(newItem);
      }
    },
    shuffleArray() {
      const halfCount = Math.round(this.componentsList.length / 2);
      const newArray = [];
      const arr1 = this.componentsList.slice(0, halfCount);
      const arr2 = this.componentsList.slice(halfCount);
      for (let i = 0; i < halfCount; i++) {
        newArray.push(arr1[i]);
        if (i === halfCount - 1 && this.componentsList.length % 2 !== 0) break;
        newArray.push(arr2[i]);
      }
      this.componentsListShuffled = newArray;
    },
  },
};
</script>

<style lang="scss" src="./CatalogModalComponent.scss" scoped/>