<template>
  <component
    :is="isSelect?'div':'label'"
    class="filter-item"
    :class="{'filter-item--selected': isSelect}"
  >
    <input
      ref="checkbox"
      v-model="checked"
      type="checkbox"
      :value="modelValue"
    >
    <slot />
    <CloseButton
      v-if="isSelect"
      class="filter-item__close"
      small
      blue
      @click="$refs.checkbox.click()"
    />
  </component>
</template>

<script>

import CloseButton from '@components/Buttons/CloseButton/CloseButton';
export default {
  name: 'FilterItem',
  components: {
    CloseButton, 
  },
  props:{
    modelValue:{
      type: [ String, Number, Boolean, Object ],
      default: () => '',
    },
    value:{
      type:[ Array, Boolean ],
      default: () => [],
    },
  },
  computed:{
    isSelect(){
      return this.value.includes(this.modelValue);
    },
    checked:{
      get(){
        return this.value;
      },
      set(value){
        this.$emit('input', value);
      },
    },
  },
};
</script>
<style lang="scss" src="./FilterItem.scss" scoped />
