<template>
  <div class="product-detail-slider">
    <div class="thumbs-slider">
      <CornerArrowButton
        class="thumbs-slider__control thumbs-slider__control--desktop thumbs-slider__control--prev"
        up
      />
      <CornerArrowButton
        class="thumbs-slider__control thumbs-slider__control--desktop thumbs-slider__control--next"
        down
      />
      <CornerArrowButton
        class="thumbs-slider__control thumbs-slider__control--mobile thumbs-slider__control--prev"
      />
      <CornerArrowButton
        class="thumbs-slider__control thumbs-slider__control--mobile thumbs-slider__control--next"
        right
      />
      <Swiper
        ref="swiperThumbs"
        :options="thumbsOption"
        class="thumbs-slider__slider"
      >
        <SwiperSlide
          v-for="(sliderImage,i) in sliderImages"
          :key="i"
        >
          <div
            class="thumbs-slide"
            :class="{'thumbs-slide--active':activeIndex===i}"
            @click="selectSlide(i)"
          >
            <img
              :src="getImg(sliderImage.preview)"
              :alt="getImg(sliderImage.preview)"
            >
          </div>
        </SwiperSlide>
      </Swiper>
    </div>
    <div class="main-slider">
      <BadgeList class="main-slider__badges">
        <BadgeItem background="#97E0C5">
          Акция
        </BadgeItem>
      </BadgeList>
      <LikeCheckbox
        v-model="isFavorite"
        class="main-slider__favorite"
      />
      <Swiper
        ref="swiperSlider"
        :options="sliderOptions"
        class="main-slider__slider"
      >
        <SwiperSlide
          v-for="(sliderImage,i) in sliderImages"
          :key="i"
          class="main-slide"
        >
          <image-zoom
            :regular="getImg(sliderImage.main)"
            img-class="main-slide__image"
          />
        </SwiperSlide>
      </Swiper>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import BadgeList from '@components/BadgeList/BadgeList';
import BadgeItem from '@components/BadgeList/BadgeItem/BadgeItem';
import LikeCheckbox from '@components/LikeCheckbox/LikeCheckbox';
import CornerArrowButton from '@components/CornerArrowButton/CornerArrowButton';
import imageZoom from 'vue-image-zoomer';

export default {
  name: 'ProductDetailSlider',
  components: {
    CornerArrowButton,
    LikeCheckbox,
    BadgeItem,
    BadgeList,
    Swiper,
    SwiperSlide,
    imageZoom,
  },
  data() {
    return {
      isFavorite:false,
      sliderImages:[
        {
          preview:'preview-1.png',
          main:'preview-1.png',
        },
        {
          preview:'preview-2.png',
          main:'main-2.png',
        },
        {
          preview:'preview-3.png',
          main:'preview-3.png',
        },
        {
          preview:'preview-4.png',
          main:'preview-4.png',
        },
        {
          preview:'preview-1.png',
          main:'preview-1.png',
        },
        {
          preview:'preview-2.png',
          main:'main-2.png',
        },
        {
          preview:'preview-3.png',
          main:'preview-3.png',
        },
        {
          preview:'preview-4.png',
          main:'preview-4.png',
        },
      ],
      activeIndex:0,
      thumbsOption: {
        spaceBetween: 8,
        slidesPerView: 4,
        freeMode: true,
        navigation: {
          prevEl: '.thumbs-slider__control--prev',
          nextEl: '.thumbs-slider__control--next',
          disabledClass:'corner-arrow-button--disabled',
        },
        breakpoints:{
          780:{
            slidesPerView: 8,
          },
          1024:{
            direction: 'vertical',
            slidesPerView: 4,
          },
        },
      },
      sliderOptions: {
        spaceBetween: 8,
        slidesPerView: 1,
        allowTouchMove:false,
        speed:0,
      },
    };
  },
  methods:{
    selectSlide(index){
      this.activeIndex = index;
      this.$refs.swiperSlider.$swiper.slideTo(index);
    },
    getImg(image){
      return require(`@images/ProductPage/ProductDetail/ProductDetailSlider/${image}`);
    },
  },

};
</script>

<style lang="scss" src="./ProductDetailSlider.scss" scoped/>