<template>
  <SectionTemplate
    class="brand-quota"
    :class="classes"
  >
    <h2 class="brand-quota__title">
      {{ title }}
    </h2>
    <p class="brand-quota__text">
      {{ text }}
    </p>
    <h4 class="brand-quota__subtext">
      {{ subtext }}
    </h4>
    <a
      class="brand-quota__more"
      @click="isHidden=!isHidden"
    >
      {{ isHidden ? 'Ещё' : 'Скрыть' }}
    </a>
  </SectionTemplate>
</template>

<script>
import SectionTemplate from '@templates/SectionTemplate/SectionTemplate';

export default {
  name: 'BrandQuota',
  components: {
    SectionTemplate,
  },
  props: {
    pink: {
      type: Boolean,
      default: () => false,
    },
    title: {
      type: String,
      default: () => '',
    },
    text: {
      type: String,
      default: () => '',
    },
    subtext: {
      type: String,
      default: () => '',
    },
  },
  data() {
    return {
      isHidden: true,
    };
  },
  computed: {
    classes() {
      return {
        'brand-quota--pink': this.pink,
        'brand-quota--hidden': this.isHidden,
      };
    },
  },
};
</script>

<style lang="scss" src="./BrandQuota.scss" scoped/>