import ProductCard from '@components/ProductCard/ProductCard';
import 'swiper/css/swiper.min.css';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import SectionTemplate from '@templates/SectionTemplate/SectionTemplate';
import UiMobilePagination from '@ui/UiMobilePagination/UiMobilePagination';
import CornerArrowButton from '@components/CornerArrowButton/CornerArrowButton';
import FlowerItem from '@components/FlowerItem/FlowerItem';

export default {
  name: 'ProductsSlider',
  components:{
    FlowerItem,
    CornerArrowButton,
    UiMobilePagination,
    SimpleArrowButton,
    BlockHeading,
    SectionTemplate,
    ProductCard,
    Swiper,
    SwiperSlide,
    MainSwitcher,
  },
  props:{
    switcher:{
      type: Boolean,
      default: false,
    },
    suptitle:{
      type: String,
      default: '',
    },
    title:{
      type: String,
      default: '',
    },
    main:{
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      swiperOptions: {
        loop: false,
        breakpoints:{
          360: {
            slidesPerView: 1.15,
            spaceBetween: 10,
          },
          780: {
            slidesPerView: 2.3,
            spaceBetween: 10,
          },
          1024: {
            slidesPerView: 3.1,
            spaceBetween: 10,
          },
          1280: {
            slidesPerView: 4,
            spaceBetween: 20,
          },
        },
        navigation: {
          nextEl: '.products-slider__button--next',
          prevEl: '.products-slider__button--prev',
        },
        pagination: {
          el: '.products-slider__pagination',
          type: 'bullets',
          clickable: true,
        },
      },
    };
  },
  computed:{
    classes(){
      return {
        'products-slider--main':this.main,
      };
    },
  },
};
</script>

