          image:'type1.jpg',
          kindOfAnimation:1,
          positionImg:'right',
          text:'Мы заботимся о безопасности своей продукции и здоровье потребителей, постоянно улучшая и обеспечивая контроль качества в течение всего жизненного цикла препаратов: от стадии разработки до этапа проверки выпускаемой продукции',
          button: 'О нашем подходе к качеству',
        },
      ],
    };
  },
};
</script>

<style lang="scss" src="./NotesWrapper.scss" scoped/>
