<template>
  <div class="video-card">
    <PopupGallery
      v-if="openPopup"
      :gallery="gallery"
      @closePopup="openPopup = !openPopup"
    />
    <div class="video-card__preview">
      <div class="video-card__shadow" />
      <img
        :src="require(`@images/${image}`)"
        alt="preview"
      >
      <UiButton
        class="ui-button--circle ui-button--play video-card__play-btn"
        text="смотреть"
        @click="addPopup"
      >
        <PlayIcon v-if="windowWidth >=1024" />
        <PlayIcon2 v-else />
        <div class="ui-button__background-play" />
      </UiButton>
    </div>
    <div class="video-card__title">
      {{ title }}
    </div>
  </div>
</template>

<script>
import UiButton from '@ui/UiButton/UiButton';
import PlayIcon from '@icons/UiComponent/UiButton/PlayIcon.svg';
import PlayIcon2 from '@icons/UiComponent/UiButton/PlayIcon2.svg';
import PopupGallery from '@components/PopupGallery/PopupGallery';

export default {
  name: 'VideoCard',
  components:{
    UiButton,
    PlayIcon,
    PlayIcon2,
    PopupGallery,
  },
  props:{
    image:{
      type:String,
      default:'',
    },
    title: {
      type:String,
      default:'',
    },
    gallery: {
      type:Array,
      default:() => [],
    },
  },
  data(){
    return{
      openPopup:false,
    };
  },
  computed:{
    windowWidth(){
      return window.innerWidth;
    },
  },
  methods:{
    addPopup(){
      this.openPopup = !this.openPopup;
      document.documentElement.style.overflow = 'hidden';
    },
  },


};
</script>

<style lang="scss" src="./VideoCard.scss" scoped>

</style>
