<template>
  <div class="star-rating">
    <form
      class="star-rating__wrap"
      :class="{'star-rating__wrap--show':show === true}"
    >
      <template v-for="count in array">
        <label
          v-if="!show"
          :key="'input-'+count"
          class="star-rating__ico fa fa-star-o fa-lg"
          :class="{'show':show === true}"
          :title="count + ' out of 5 stars'"
        >
          <input
            :id="'star-rating-'+count"
            class="star-rating__input"
            :class="{'show':show === true}"
            type="checkbox"
            name="rating"
            :checked="(mark>= Number(count))"
            :value="count"
            @click="addMark(count)"
          >
          <Star />
        </label>
        <div
          v-else
          :key="'star-'+count"
          class="star-rating__wrp-svg"
          :class="{'checked':(mark>= Number(count))}"
        >
          <Star />
        </div>
      </template>
    </form>
  </div>
</template>

<script>
import Star from '@icons/StarRating/Star.svg';

export default {
  name: 'StarRating',
  components:{
    Star, 
  },
  props:{
    mark:{
      type:Number,
      default:0,
    },
    show:{
      type:Boolean,
      default: false,
    },
  },
  data(){
    return {
      maxCount:5,
      array:[],
    };
  },
  mounted() {
    let x = [];
    for (let i=1; i<=this.maxCount; i++){
      x = [ ...x, i ];
    }
    if (!this.show){
      this.array = x.reverse();
    } else {
      this.array = x;
    }
  },
  methods:{
    addMark(count){
      this.$emit('update', count);
    },
  },
};
</script>
<style lang="scss" src="./StarRating.scss" scoped/>

