<template>
  <div class="filter-list-item">
    <h6 class="filter-list-item__title">
      По активному компоненту
    </h6>
    <LettersList
      v-model="letterModel"
      class="filter-list-item__letters"
      light
    />
    <ScrollBlock class="filter-list-item__scroll-block">
      <div
        v-for="filterItem in filterItems"
        :key="filterItem.letter"
        class="filter-list-item__block"
      >
        <p class="filter-list-item__letter">
          {{ filterItem.letter }}
        </p>
        <FilterItem
          v-for="(item,i) in filterItem.items"
          :key="i"
          v-model="filterModels[filterItem.letter]"
          :model-value="item"
          class="filter-list-item__filter-button"
        >
          {{ item }}
        </FilterItem>
      </div>
    </ScrollBlock>
  </div>
</template>

<script>
import LettersList from '@components/LettersList/LettersList';
import ScrollBlock from '@components/ScrollBlock/ScrollBlock';
import FilterItem from '@components/FilterItem/FilterItem';

export default {
  name: 'FilterListItem',
  components: {
    FilterItem,
    ScrollBlock,
    LettersList,
  },
  props: {
    filterItems: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      letterModel: '',
      filterModels: {},
    };
  },
};
</script>

<style lang="scss" src="./FilterListItem.scss" scoped/>