<template>
  <SectionTemplate class="abc-health">
    <FlowerItem
      flower-type="2"
      class="abc-health__flower abc-health__flower--1"
    />
    <FlowerItem
      flower-type="5"
      class="abc-health__flower abc-health__flower--2"
    />
    <BreadCrumbs
      class="abc-health__breadcrumbs"
      :links="links"
    />
    <BlockHeading
      class="abc-health__heading"
      title="Азбука здоровья"
    />
    <LettersList
      v-model="letterModel"
      class="abc-health__letters letters-swiper--desktop"
    />
    <div
      class="letters-swiper--mobile"
    >
      <CornerArrowButton
        class="letters-swiper__button letters-swiper__button--prev"
      />
      <CornerArrowButton
        right
        class="letters-swiper__button letters-swiper__button--next"
      />
      <Swiper
        :options="swiperOption"
      >
        <SwiperSlide>
          <LettersList
            v-model="letterModel"
            class="abc-health__letters"
          />
        </SwiperSlide>
      </Swiper>
    </div>
    <div class="abc-health__ingredients">
      <ul>
        <li
          v-for="i in 100"
          :key="i"
        >
          <IngredientCard img="ingredient.jpg">
            Боярышник
          </IngredientCard>
        </li>
      </ul>
    </div>
  </SectionTemplate>
</template>

<script>
import SectionTemplate from '@templates/SectionTemplate/SectionTemplate';
import BreadCrumbs from '@components/BreadCrumbs/BreadCrumbs';
import BlockHeading from '@components/BlockHeading/BlockHeading';
import LettersList from '@components/LettersList/LettersList';
import IngredientCard from '@components/IngredientCard/IngredientCard';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import CornerArrowButton from '@components/CornerArrowButton/CornerArrowButton';
import FlowerItem from '@components/FlowerItem/FlowerItem';

export default {
  name: 'AbcHealth',
  components: {
    FlowerItem,
    CornerArrowButton,
    IngredientCard,
    LettersList,
    BlockHeading,
    BreadCrumbs,
    SectionTemplate,
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      letterModel: '',
      links: [
        {
          name: 'Азбука здоровья',
          href: '/abc-health',
        },
      ],
      swiperOption: {
        direction: 'horizontal',
        freeMode: true,
        navigation: {
          nextEl: '.letters-swiper__button--next',
          prevEl: '.letters-swiper__button--prev',
          disabledClass:'corner-arrow-button--disabled',
        },

        breakpoints: {
          730: {
            slidesPerView: '0.55',
          },
          600: {
            slidesPerView: '0.45',
          },
          510: {
            slidesPerView: '0.375',
          },
          450: {
            slidesPerView: '0.325',
          },
          400: {
            slidesPerView: '0.285',
          },
          350: {
            slidesPerView: '0.245',
          },
          320: {
            slidesPerView: '0.219',
          },
        },
      },
    };
  },
};
</script>

<style lang="scss" src="./AbcHealth.scss" scoped/>
