<template>
  <div
    class="brand-card"
    :class="[classContainer, `brand-card--${color}`]"
  >
    <div
      v-if="image"
      class="brand-card__medicine-image"
    >
      <img
        class="brand-card__desktop-img"
        :src="getImg(image)"
        alt="лекарства"
      >
    </div>
    <div
      v-if="imageMobile"
      class="brand-card__medicine-image brand-card__medicine-image--mobile"
    >
      <img
        :src="getImg(imageMobile)"
        alt="лекарства"
      >
    </div>
    <div class="brand-card__logo">
      <!-- eslint-disable vue/no-v-html -->
      <div
        class="brand-card__logo-svg"
        v-html="logoSvg"
      />
      <!-- eslint-enable -->
      <img
        v-if="logo"
        :src="getImg(logo)"
        alt="logo"
      >
      <h4 v-if="title">
        {{ title }}
      </h4>
    </div>
    <div
      class="brand-card__content"
    >
      <div class="brand-card__text">
        {{ text }}
      </div>

      <UiLink
        class="brand-card__link"
        to="#"
      >
        Подробнее
        <Arrow />
      </UiLink>
    </div>
    <div class="brand-card__mobile-content">
      <div class="brand-card__logo brand-card__logo--mobile">
        <!-- eslint-disable vue/no-v-html -->
        <div
          class="brand-card__logo-svg-mobile"
          v-html="logoSvg"
        />
        <!-- eslint-enable -->
        <img
          v-if="logo"
          :src="getImg(logo)"
          alt="logo"
        >
        <h4 v-if="title">
          {{ title }}
        </h4>
      </div>
      <div class="brand-card__category">
        {{ category }}
      </div>
      <UiLink
        class="brand-card__link"
        to="#"
      >
        Подробнее
        <Arrow />
      </UiLink>
    </div>
    <div
      class="brand-card__image"
      :class="color === 'green' && 'brand-card__image--green'"
    >
      <img
        :src="actualImageLight"
        alt="image"
      >
    </div>
    <div
      class="brand-card__image2"
      :class="color === 'green' && 'brand-card__image2--green'"
    >
      <img
        :src="actualImageDark"
        alt="image"
      >
    </div>
    <div class="brand-card__mobile-bg">
      <img
        :src="mobileBg"
        alt="image"
      >
    </div>
    <div
      v-if="mobileBg && veryLong"
      class="brand-card__mobile-bg2"
    >
      <img
        :src="mobileBgSecond"
        alt="image"
      >
    </div>
  </div>
</template>

<script>
import Arrow from '@icons/BrandCard/link-arrow.svg';
import UiLink from '@ui/UiLink/UiLink';
export default {
  name: 'BrandCard',
  components: {
    UiLink,
    Arrow,
  },
  props:{
    color:{
      type: String,
      default:'',
    },
    long:{
      type: Boolean,
      default: false,
    },
    veryLong:{
      type: Boolean,
      default: false,
    },
    text:{
      type:String,
      default:'',
    },
    category:{
      type:String,
      default:'',
    },
    logo:{
      type:String,
      default:'',
    },
    title:{
      type:String,
      default:'',
    },
    image :{
      type:String,
      default:'',
    },
    imageMobile :{
      type:String,
      default:'',
    },
    colorLink:{
      type:String,
      default:'',
    },
    logoSvg:{
      type:String,
      default:'',
    },
    showSomeCards:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    classContainer(){
      return {
        'brand-card--long': this.long,
        'brand-card--very-long': this.veryLong,
        'brand-card--very-long-all': !this.showSomeCards,
        'brand-card--very-long-not-all': this.showSomeCards,
      };
    },
    actualImageLight(){
      if (this.color === 'pink') return this.getImg('pink/pinkLight.png');
      else if (this.color === 'biruse') return this.getImg('biruse/biruseLight.png');
      else if (this.color === 'yellow') return this.getImg('yellow/yellowLight.png');
      else if (this.color === 'red') return this.getImg('red/redLight.png');
      else if (this.color === 'violet') return this.getImg('violet/violetLight.png');
      else if (this.color === 'gray') return this.getImg('gray/grayLight.png');
      else if (this.color === 'blue') return this.getImg('blue/blueLight.png');
      else if (this.color === 'green') return this.getImg('green/greenLight.png');
      else return '';
    },
    actualImageDark(){
      if (this.color === 'pink') return this.getImg('pink/pinkDark.png');
      else if (this.color === 'biruse') return this.getImg('biruse/biruseDark.png');
      else if (this.color === 'yellow') return this.getImg('yellow/yellowDark.png');
      else if (this.color === 'red') return this.getImg('red/redDark.png');
      else if (this.color === 'violet') return this.getImg('violet/violetDark.png');
      else if (this.color === 'gray') return this.getImg('gray/grayDark.png');
      else if (this.color === 'blue') return this.getImg('blue/blueDark.png');
      else if (this.color === 'green') return this.getImg('green/greenDark.png');
      else return '';
    },

    mobileBg(){
      if (this.color === 'pink') return this.getImg('pink/mobileBg.png');
      else if (this.color === 'biruse') return this.getImg('biruse/mobileBg.png');
      else if (this.color === 'yellow') return this.getImg('yellow/mobileBg.png');
      else if (this.color === 'red') return this.getImg('red/mobileBg.png');
      else if (this.color === 'violet' && window.innerWidth >= 780) return this.getImg('violet/mobileBg2.png');
      else if (this.color === 'violet' && window.innerWidth < 780) return this.getImg('violet/mobileBg.png');
      else if (this.color === 'gray') return this.getImg('gray/mobileBg.png');
      else if (this.color === 'blue') return this.getImg('blue/mobileBg.png');
      else if (this.color === 'green') return this.getImg('green/mobileBg.png');
      else return '';
    },

    mobileBgSecond(){
      if (this.color === 'violet'){
        return this.getImg('violet/mobileBg.png');
      } else return '';
    },
  },
  methods:{
    getImg(image) {
      return require(`@images/BrandCard/${image}`);
    },
  },

};
</script>

<style lang="scss" src="./BrandCard.scss" scoped>

</style>
