<template>
  <SectionTemplate
    class="color-block"
    :class="classes"
  >
    <FlowerItem
      v-if="color === 'blue'"
      flower-type="1"
      class="color-block__flower color-block__flower--blue"
    />
    <FlowerItem
      v-if="color === 'pink'"
      flower-type="4"
      class="color-block__flower color-block__flower--pink"
    />
    <div class="color-block__article">
      <div class="color-block__theme">
        {{ theme }}
      </div>
      <div class="color-block__content">
        <h2>{{ title }}</h2>
        <div class="color-block__text">
          <p>{{ text }}</p>
          <UiButton
            v-if="textButton"
            :light-blue="color === 'blue'"
            :pink="color === 'pink'"
            :to="href"
            class="color-block__button"
          >
            {{ textButton }}
          </UiButton>
        </div>
      </div>
      <UiButton
        v-if="textButton"
        :light-blue="color === 'blue'"
        :pink="color === 'pink'"
        class="color-block__button color-block__button--mobile"
        :to="href"
      >
        {{ textButton }}
      </UiButton>
    </div>
  </SectionTemplate>
</template>

<script>
import UiButton from '@ui/UiButton/UiButton';
import SectionTemplate from '@templates/SectionTemplate/SectionTemplate';
import FlowerItem from '@components/FlowerItem/FlowerItem';
export default {
  name: 'ColorBlock',
  components: {
    FlowerItem,
    SectionTemplate,
    UiButton,
  },
  props:{
    theme:{
      type:String,
      default:'',
    },
    title:{
      type:String,
      default:'',
    },
    text:{
      type:String,
      default:'',
    },
    textButton:{
      type:String,
      default:'',
      required: false,
    },
    color:{
      type:String,
      default:'',
    },
    href:{
      type:String,
      default:'#',
    },
    main:{
      type:Boolean,
      default:()=> false,
    },
  },
  computed:{
    classes(){
      return {
        ['color-block--'+this.color]:true,
        'color-block--main':this.main,
      };
    },
  },
};
</script>

<style lang="scss" src="./ColorBlock.scss" scoped/>
