<template>
  <teleport
    to="body"
  >
    <div
      class="header-mobile-popup"
      :class="{'header-mobile-popup--active':value}"
      @click="$emit('input',false)"
    >
      <div
        class="header-mobile-popup__inner"
        @click.stop
      >
        <div
          class="header-mobile-popup__header"
          :class="{'header-mobile-popup__header--search':isShowSearch}"
        >
          <div
            v-if="!isShowSearch"
            class="header-mobile-popup__header-logo"
          >
            <LogoIcon v-if="!isCatalogOpen" />
            <button
              v-if="isCatalogOpen"
              @click="doBack"
            >
              <ArrowBack />
              Назад
            </button>
          </div>
          <HiddenSearchInput
            v-model="isShowSearch"
            big
          >
            <SearchInput
              v-model="searchStr"
              :search-hints="searchHints"
              placeholder="Поиск товара"
              active-placeholder="Введите первые буквы"
            />
          </HiddenSearchInput>
          <CloseButton
            class="header-mobile-popup__close-button"
            square
            @click="$emit('input',false)"
          />
        </div>
        <div class="header-mobile-popup__body">
          <nav
            v-if="!isCatalogOpen"
            class="header-mobile-popup__nav header-mobile-popup__nav--first"
          >
            <ul>
              <li>
                <a @click="openCatalog">
                  <CatalogIcon />
                  Каталог товаров
                  <ArrowRight />
                </a>
              </li>
              <li
                v-for="(link,index) in navLinks"
                :key="index"
              >
                <router-link :to="link.href">
                  <MapIcon v-if="link.mapIcon" />
                  {{ link.name }}
                </router-link>
              </li>
            </ul>
          </nav>
          <nav
            v-if="isCatalogOpen"
            class="header-mobile-popup__nav"
          >
            <ul>
              <li
                v-for="(item,index) in filterItems"
                :key="index"
                @click="changeTab(item.value)"
              >
                <a>
                  {{ item.name }}
                  <ArrowRight />
                </a>
              </li>
            </ul>
          </nav>
          <CatalogModalVector v-if="activeTab === 'vector'" />
          <CatalogModalComponent v-if="activeTab === 'component'" />
          <div
            v-if="!isCatalogOpen"
            class="header-mobile-popup__slogan"
          >
            <LogoSlogan />
          </div>
          <label class="header-mobile-popup__fav">
            <LikeCheckbox v-model="isFavoriteModalShow" />
            Избранное
          </label>
        </div>
        <slot />
      </div>
    </div>
  </teleport>
</template>

<script>
import teleport from 'vue2-teleport';
import CloseButton from '@components/Buttons/CloseButton/CloseButton';
import HiddenSearchInput from '@components/HiddenSearchInput/HiddenSearchInput';
import SearchInput from '@components/SearchInput/SearchInput';
import LogoIcon from '@icons/AppHeader/logo-header-mobile.svg';
import LogoSlogan from '@icons/AppHeader/HeaderMobilePopup/slogan.svg';
import { mapActions, mapGetters } from 'vuex';
import MapIcon from '@icons/AppHeader/map-icon.svg';
import CatalogIcon from '@icons/AppHeader/catalog-icon.svg';
import ArrowRight from '@icons/AppHeader/HeaderMobilePopup/arrow-right.svg';
import ArrowBack from '@icons/AppHeader/HeaderMobilePopup/arrow-back.svg';
import LikeCheckbox from '@components/LikeCheckbox/LikeCheckbox';
import CatalogModalVector from '@components/CatalogModal/CatalogModalVector/CatalogModalVector';
import CatalogModalComponent from '@components/CatalogModal/CatalogModalComponent/CatalogModalComponent';

export default {
  name: 'HeaderMobilePopup',
  components: {
    CatalogModalComponent,
    CatalogModalVector,
    LikeCheckbox,
    SearchInput,
    HiddenSearchInput,
    CloseButton,
    teleport,
    LogoIcon,
    LogoSlogan,
    MapIcon,
    CatalogIcon,
    ArrowRight,
    ArrowBack,
  },
  props: {
    value: {
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      isShowSearch: false,
      searchHints: [],
      isCatalogOpen: false,
      activeTab: '',

    };
  },
  computed: {
    ...mapGetters([ 'navLinks', 'searchQuery', 'isFavoriteShow', 'filterItems' ]),
    searchStr: {
      get() {
        return this.searchQuery;
      },
      set(val) {
        this.setSearchQuery(val);
      },
    },
    isFavoriteModalShow: {
      get() {
        return this.isFavoriteShow;
      },
      set(val) {
        this.setIsFavoriteShow(val);
      },
    },

  },
  watch: {
    value(value) {
      if (value) document.documentElement.style.overflowY = 'hidden';
      else document.documentElement.style.overflowY = 'inherit';
    },
  },
  methods: {
    ...mapActions([ 'setSearchQuery', 'setIsFavoriteShow' ]),
    openCatalog() {
      this.isCatalogOpen = true;
    },
    doBack(){
      if  (this.activeTab) {
        this.activeTab = false;
        return;
      }
      if (this.isCatalogOpen) this.isCatalogOpen = false;
    },
    changeTab(value){
      this.activeTab = value;
    },
  },
};
</script>

<style lang="scss" src="./HeaderMobilePopup.scss" scoped/>