<template>
  <yandex-map
    class="yandex-map"
    :settings="settings"
    :coords="coords"
    :zoom="zoom"
  >
    <ymap-marker
      :coords="coords"
      marker-id="123123"
    />
  </yandex-map>
</template>

<script>
import { yandexMap, ymapMarker } from 'vue-yandex-maps';
export default {
  name: 'YandexMap',
  components: {
    yandexMap,
    ymapMarker,
  },
  data(){
    return{
      settings:{
        apiKey: 'cb83c26b-4909-448b-a3f8-378b08b14770',
        lang: 'ru_RU',
        coordorder: 'latlong',
        enterprise: false,
        version: '2.1',
      },
      coords: [ 55.755864, 37.617698 ],
      zoom: 15,
    };
  },
};
</script>

<style lang="scss" src="./YandexMap.scss" scoped/>
