<template>
  <div class="search-result">
    <div class="search-result__index">
      <h3>
        {{ index }}
      </h3>
    </div>
    <div class="search-result__info">
      <!-- eslint-disable vue/no-v-html -->
      <h3
        class="search-result__title"
        v-html="title"
      />
      <p
        class="search-result__desc"
        v-html="desc"
      />
      <!-- eslint-enable -->
      <BreadCrumbs
        :links="breadcrumbs"
        active
      />
    </div>
  </div>
</template>

<script>
import BreadCrumbs from '@components/BreadCrumbs/BreadCrumbs';

export default {
  name: 'SearchResult',
  components: {
    BreadCrumbs,
  },
  props: {
    index: {
      type: [ Number, String ],
      default: () => 0,
    },
    title: {
      type: String,
      default: () => '',
    },
    desc: {
      type: String,
      default: () => '',
    },
    breadcrumbs: {
      type: Array,
      default: () => [],
    },
  },
};
</script>

<style lang="scss" src="./SearchResult.scss" scoped/>