<template>
  <button
    class="ui-button close-button"
    :class="classes"
    @click="$emit('click')"
    @mouseover="$emit('mouseover')"
    @mouseout="$emit('mouseout')"
  >
    <CloseIcon v-if="!small" />
    <CloseIconSmall v-else />
  </button>
</template>

<script>
import CloseIcon from '@icons/UiComponent/UiButton/CloseIcon.svg';
import CloseIconSmall from '@icons/UiComponent/UiButton/CloseIconSmall.svg';
export default {
  name: 'CloseButton',
  components:{
    CloseIcon,
    CloseIconSmall, 
  },
  props:{
    square:{
      type: Boolean,
      default: false,
    },
    gray:{
      type: Boolean,
      default: false,
    },
    small:{
      type: Boolean,
      default: false,
    },
    blue:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    classes(){
      return {
        'close-button--square':this.square,
        'close-button--gray':this.gray,
        'close-button--small':this.small,
        'close-button--blue':this.blue,
      };
    },
  },
};
</script>
<style lang="scss" scoped>

@import "../../UiComponents/UiButton/UIButton";
@import "./CloseButton";

</style>

