import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

//Pages
import MainPage from '@pages/MainPage/MainPage';
import CatalogPage from '@pages/CatalogPage/CatalogPage';
import ProductPage from '@pages/ProductPage/ProductPage';
import OurBrands from '@pages/OurBrands/OurBrands';
import BrandPage from '@pages/BrandPage/BrandPage';
import AbcHealth from '@pages/AbcHealth/AbcHealth';
import WhereBuy from '@pages/WhereBuy/WhereBuy';
import ContactsPage from '@pages/ContactsPage/ContactsPage';
import ArticlesPage from '@pages/ArticlesPage/ArticlesPage';
import ArticlesDetail from '@pages/ArticlesDetail/ArticlesDetail';
import SearchResults from '@pages/SearchResults/SearchResults';
import StocksDetail from '@pages/StocksDetail/StocksDetail';
import StocksPage from '@pages/StocksPage/StocksPage';
import AboutUs from '@pages/AboutUs/AboutUs';
import PageNotFound from '@pages/PageNotFound/PageNotFound';
import UiPage from '@pages/UiPage/UiPage';

const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage,
  },
  {
    path: '/our-brands',
    name: 'OurBrands',
    component: OurBrands,
  },
  {
    path: '/our-brands/:id',
    name: 'BrandPage',
    component: BrandPage,
  },
  {
    path: '/catalog',
    name: 'CatalogPage',
    component: CatalogPage,
  },
  {
    path: '/catalog/:id',
    name: 'ProductPage',
    component: ProductPage,
  },
  {
    path: '/stocks',
    name: 'StocksPage',
    component: StocksPage,
  },
  {
    path: '/stocks/:id',
    name: 'StocksDetail',
    component: StocksDetail,
  },
  {
    path: '/abc-health',
    name: 'AbcHealth',
    component: AbcHealth,
    meta: {
      removeMap: true,
    },
  },
  {
    path: '/where-buy',
    name: 'WhereBuy',
    component: WhereBuy,
  },
  {
    path: '/contacts',
    name: 'ContactsPage',
    component: ContactsPage,
  },
  {
    path: '/articles',
    name: 'ArticlesPage',
    component: ArticlesPage,
    meta: {
      removeMap: true,
    },
  },
  {
    path: '/articles/:id',
    name: 'ArticlesDetail',
    component: ArticlesDetail,
    meta: {
      removeMap: true,
    },
  },
  {
    path: '/search-results',
    name: 'SearchResults',
    component: SearchResults,
  },
  {
    path: '/about-us',
    name: 'AboutUs',
    component: AboutUs,
  },
  {
    path: '/ui',
    name: 'UiPage',
    component: UiPage,
  },
  {
    path: '/404',
    name: 'PageNotFound',
    component: PageNotFound,
    meta: {
      removeMap: true,
    },
  },
  {
    path: '*',
    name: 'OtherPage',
    redirect: '/404',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return {
      y: 0,
    };
  },
});

export default router;
