<template>
  <div class="animate-banners">
    <template v-if="catalogPage">
      <div class="animate-banners__list animate-banners__list--desktop">
        <AnimateBanner
          v-for="(action, index) of actions"
          :id="action.id"
          :key="index"
          :kind="action.kind"
          :title="action.title"
          :subtitle="action.subtitle"
          :image="action.image"
          :mobile-image="action.mobileImage"
          :background="action.background"
          :color-text="action.colorText"
          :price="action.price"
        />
      </div>
      <Swiper
        class="animate-banners__list--tablet"
        :options="swiperOptions"
      >
        <SwiperSlide
          v-for="(action, index) of actions"
          :key="index"
        >
          <AnimateBanner
            :id="action.id"
            class="animate-banner--in-catalog"
            :kind="action.kind"
            :title="action.title"
            :subtitle="action.subtitle"
            :image="action.image"
            :mobile-image="action.mobileImage"
            :background="action.background"
            :color-text="action.colorText"
            :price="action.price"
          />
        </SwiperSlide>
      </Swiper>
      <div
        class="animate-banners__link-wrapper animate-banners__link-wrapper--desktop"
      >
        <UiLink
          to="/stocks"
          class="animate-banners__link"
        >
          <ArrowRight />
          Все акции
        </UiLink>
      </div>
      <div class="animate-banners__controls">
        <div class="animate-banners__navigation">
          <SimpleArrowButton
            class="animate-banners__nav--prev"
          />
          <SimpleArrowButton
            class="animate-banners__nav--next"
            next-btn
          />
        </div>
        <UiMobilePagination class="animate-banners__mobile-pagination" />
      </div>
      <div
        class="animate-banners__link-wrapper animate-banners__link-wrapper--laptop"
      >
        <UiLink
          to="/stocks"
          class="animate-banners__link"
        >
          <ArrowRight />
          Все акции
        </UiLink>
      </div>
    </template>
    <template v-else>
      <div class="animate-banners__list">
        <AnimateBanner
          v-for="(action, index) of actions"
          :id="action.id"
          :key="index"
          :kind="action.kind"
          :title="action.title"
          :subtitle="action.subtitle"
          :image="action.image"
          :mobile-image="action.mobileImage"
          :background="action.background"
          :color-text="action.colorText"
          :price="action.price"
        />
      </div>
      <UiPagination
        v-if="pagination"
        class="animate-banners__pagination"
      />
    </template>
  </div>
</template>

<script>
import SimpleArrowButton from '@components/Buttons/SimpleArrowButton/SimpleArrowButton';
import AnimateBanner from '@components/AnimateBanners/AnimateBanner/AnimateBanner';
import UiPagination from '@ui/UiPagination/UiPagination';
import UiLink from '@ui/UiLink/UiLink';
import ArrowRight from '@icons/ArticleCard/arrow.svg';
import UiMobilePagination from '@ui/UiMobilePagination/UiMobilePagination';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
export default {
  name: 'AnimateBanners',
  components: {
    UiMobilePagination,
    UiLink,
    AnimateBanner,
    UiPagination,
    ArrowRight,
    Swiper,
    SwiperSlide,
    SimpleArrowButton,
  },
  props:{
    actions:{
      type:Array,
      default:() => [],
    },
    pagination:{
      type:Boolean,
      default: () => false,
    },
    catalogPage:{
      type:Boolean,
      default: () => false,
    },
  },
  data(){
    return {
      swiperOptions: {
        spaceBetween: 30,
        loop: false,
        slidesPerView: 1,
        navigation: {
          nextEl: '.animate-banners__nav--next',
          prevEl: '.animate-banners__nav--prev',
        },
        pagination: {
          el: '.animate-banners__mobile-pagination',
          type: 'bullets',
          clickable: true,
        },
      },
    };
  },
};

</script>

<style lang="scss" src="./AnimateBanners.scss" scoped/>
