<template>
  <div class="review-item">
    <div class="review-item__header">
      <h6 class="review-item__author">
        {{ author }}
      </h6>
      <StarRating
        class="review-item__rating"
        :mark="rating"
        show
      />
    </div>
    <ScrollBlock class="review-item__text">
      <!-- eslint-disable vue/no-v-html -->
      <div v-html="text" />
      <!-- eslint-enable -->
    </ScrollBlock>
  </div>
</template>

<script>
import ScrollBlock from '@components/ScrollBlock/ScrollBlock';
import StarRating from '@components/StarRating/StarRating';
export default {
  name: 'ReviewItem',
  components: {
    StarRating,
    ScrollBlock, 
  },
  props: {
    author: {
      type: String,
      default: () => '',
    },
    rating: {
      type: Number,
      default: () => 5,
    },
    text: {
      type: String,
      default: () => '',
    },
  },
};
</script>

<style lang="scss" src="./ReviewItem.scss" scoped/>