<template>
  <div
    class="category-select"
  >
    <button
      class="category-select__toggle"
      :class="{'category-select__toggle--active':value.name}"
      @click="optionsVisible=!optionsVisible"
    >
      <template v-if="value.name">
        {{ value.name }}
      </template>
      <template v-else>
        Выберите категорию
      </template>
      <SelectArrow />
    </button>
    <transition name="fade">
      <ul
        v-if="optionsVisible"
      >
        <ScrollBlock>
          <li
            v-for="option in options"
            :key="option.value"
          >
            <a @click="selectOption(option)">
              {{ option.name }}
            </a>
          </li>
        </ScrollBlock>
      </ul>
    </transition>
    <div
      class="category-select__dropdown"
      :class="{'category-select__dropdown--active':optionsVisible}"
    />
  </div>
</template>

<script>
import SelectArrow from '@icons/CategorySelect/arrow.svg';
import ScrollBlock from '@components/ScrollBlock/ScrollBlock';
export default {
  name: 'CategorySelect',
  components:{
    ScrollBlock,
    SelectArrow, 
  },
  props: {
    value:{
      type:Object,
      default:()=>({}),
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      optionsVisible: false,
    };
  },
  methods: {
    selectOption(option) {
      this.$emit('input', option);
      this.optionsVisible = false;
    },
  },

};
</script>

<style lang="scss" src="./CategorySelect.scss" scoped/>