<template>
  <label class="like-checkbox">
    <input
      v-model="checked"
      type="checkbox"
      :value="modelValue"
    >
    <LikeIcon />
  </label>
</template>
<script>
import LikeIcon from '@icons/LikeCheckbox/LikeIcon.svg';

export default {
  components: {
    LikeIcon, 
  },
  props:{
    modelValue:{
      type: [ String, Number, Boolean, Object ],
      default: () => '',
    },
    value:{
      type:[ Array, Boolean ],
      default: () => [],
    },
  },
  computed:{
    checked:{
      get(){
        return this.value;
      },
      set(value){
        this.$emit('input', value);
      },
    },
  },
};
</script>
<style lang="scss" src="./LikeCheckbox.scss" scoped/>
