<template>
  <div class="animate-plus-yellow">
    <Background />
    <img
      :src="getImg(image)"
      alt="image"
      class="animate-plus-yellow__image"
    >
    <div class="animate-plus-yellow__text">
      {{ text }}
    </div>
    <div class="animate-plus-yellow__hover-text">
      {{ hoverText }}
    </div>
    <div class="animate-plus-yellow__circle-top1">
      <CircleTop />
    </div>
    <div class="animate-plus-yellow__circle-bottom1">
      <CircleBottom1 />
    </div>
    <div class="animate-plus-yellow__circle-bottom2">
      <CircleBottom2 />
    </div>
  </div>
</template>

<script>
import Background from '@icons/AnimatePlus/yellow/bg-svg.svg';
import CircleTop from '@icons/AnimatePlus/yellow/circleTop.svg';
import CircleBottom1 from '@icons/AnimatePlus/yellow/circleBottom1.svg';
import CircleBottom2 from '@icons/AnimatePlus/yellow/circleBottom1.svg';
export default {
  name: 'AnimatePlusYellow',
  components: {
    Background,
    CircleTop,
    CircleBottom1,
    CircleBottom2,
  },
  props:{
    image:{
      type:String,
      default:'',
    },
    text:{
      type: String,
      default: '',
    },
    hoverText:{
      type: String,
      default: '',
    },

  },
  methods:{
    getImg(image) {
      return require(`@images/AnimatePlus/${image}`);
    },
  },

};
</script>

<style lang="scss" src="./AnimatePlusYellow.scss" scoped>

</style>
