<template>
  <div
    v-if="value"
    class="catalog-modal"
  >
    <div class="catalog-modal__left">
      <ul>
        <li
          v-for="item in filterItems"
          :key="item.value"
        >
          <a
            @click="changeTab(item.value)"
          >
            <span :class="{'active':item.value===activeTab}">
              {{ item.name }}
            </span>
          </a>
        </li>
      </ul>
      <div class="catalog-modal__catalog-button">
        <UiButton to="/catalog">
          Весь каталог
        </UiButton>
      </div>
    </div>
    <div class="catalog-modal__right">
      <CatalogModalVector v-if="activeTab === 'vector'" />
      <CatalogModalComponent v-if="activeTab === 'component'" />
    </div>
    <CloseIcon
      class="catalog-modal__close"
      @click="closeModal"
    />
  </div>
</template>

<script>
import CloseIcon from '@icons/CatalogModal/close.svg';
import UiButton from '@ui/UiButton/UiButton';
import CatalogModalVector from '@components/CatalogModal/CatalogModalVector/CatalogModalVector';
import CatalogModalComponent from '@components/CatalogModal/CatalogModalComponent/CatalogModalComponent';
import { mapGetters } from 'vuex';

export default {
  name: 'CatalogModal',
  components: {
    CatalogModalComponent,
    CatalogModalVector,
    UiButton,
    CloseIcon, 
  },
  props: {
    value: {
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      activeTab: '',
    };
  },
  computed:{
    ...mapGetters([ 'filterItems' ]),
  },
  watch: {
    value(value) {
      if (value) document.documentElement.style.overflowY = 'hidden';
      else document.documentElement.style.overflowY = 'inherit';
    },
  },
  methods: {
    changeTab(value) {
      this.activeTab = value;
    },
    closeModal() {
      this.$emit('input', false);
    },
  },
};
</script>

<style lang="scss" src="./CatalogModal.scss" scoped/>