<template>
  <div
    class="hidden-search-input"
    :class="{'hidden-search-input--active':value}"
  >
    <slot v-if="value" />
    <component
      :is="whatSearchIcon"
      v-if="!value"
      class="hidden-search-input__search-icon"
      @click="setVisible(true)"
    />
    <CloseButton
      v-else
      gray
      small
      @click="setVisible(false)"
    />
  </div>
</template>

<script>
import SearchIcon from '@icons/SearchInput/search.svg';
import SearchIconBig from '@icons/SearchInput/search-big.svg';
import CloseButton from '@components/Buttons/CloseButton/CloseButton';
export default {
  name: 'HiddenSearchInput',
  components:{
    CloseButton,
    SearchIcon,
    SearchIconBig,
  },
  props:{
    value:{
      type: Boolean,
      default:()=> false,
    },
    big:{
      type: Boolean,
      default:()=> false,
    },
  },
  computed:{
    whatSearchIcon(){
      return this.big? 'SearchIconBig' : 'SearchIcon';
    },
  },
  methods:{
    setVisible(value){
      this.$emit('input', value);
    },
  },
};
</script>

<style lang="scss" src="./HiddenSearchInput.scss" scoped/>
