<template>
  <div
    class="text-input"
    :class="inputClasses"
  >
    <input
      type="text"
      :value="value"
      placeholder=" "
      @input="$emit('input',$event.target.value)"
      @focus="$emit('focus')"
      @blur="$emit('blur')"
    >
    <span
      class="text-input__placeholder"
    >
      {{ placeholder }}
    </span>
    <div class="text-input__icon">
      <ErrorCross v-if="isError" />
      <SuccessCheck v-else-if="isSuccess" />
    </div>
    <div
      class="text-input__error-message"
    >
      {{ errorText }}
    </div>
  </div>
</template>
<script>
import SuccessCheck from '@icons/UiComponent/TextInput/SuccessCheck.svg';
import ErrorCross from '@icons/UiComponent/TextInput/ErrorCross.svg';

export default {
  name: 'TextInput',
  components: {
    SuccessCheck,
    ErrorCross,
  },
  props: {
    value: {
      type: String,
      default: () => '',
    },
    placeholder: {
      type: String,
      default: () => '',
    },
    errorText: {
      type: String,
      default: () => '',
    },
    isSuccess: {
      type: Boolean,
      default: () => false,
    },
    isError: {
      type: Boolean,
      default: () => false,
    },
  },
  computed: {
    inputClasses() {
      return {
        'text-input--success': this.isSuccess,
        'text-input--error': this.isError,
      };
    },
  },
};
</script>
<style lang="scss" src="./TextInput.scss" scoped/>
