<template>
  <div
    id="product-detail-tabs"
    class="product-detail-tabs"
  >
    <div class="tabs-header product-detail-tabs__header">
      <ul>
        <li
          v-for="(item,i) in tabsList"
          :key="i"
        >
          <a
            :class="{active:item.value===activeTab}"
            :href="item.anchor"
            @click="selectTab(item.value)"
          >
            {{ item.name }}
          </a>
        </li>
      </ul>
    </div>


    <div class="tabs-body">
      <div
        v-if="activeTab === 'description'"
        class="tabs-body__item"
      >
        <DescriptionItem />
      </div>
      <div
        v-if="activeTab === 'structure'"
        class="tabs-body__item"
      >
        <StructureItem />
      </div>

      <div
        v-if="activeTab === 'instruction'"
        class="tabs-body__item"
      >
        <InstructionItem />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import DescriptionItem from '@pages/ProductPage/ProductDetail/InfoItems/DescriptionItem/DescriptionItem';
import StructureItem from '@pages/ProductPage/ProductDetail/InfoItems/StructureItem/StructureItem';
import InstructionItem from '@pages/ProductPage/ProductDetail/InfoItems/InstructionItem/InstructionItem';

export default {
  name: 'ProductDetailTabs',
  components: {
    InstructionItem,
    StructureItem,
    DescriptionItem,
  },
  data() {
    return {
      activeTab: 'description',
    };
  },
  computed: {
    ...mapGetters([ 'tabsList' ]),
  },
  methods: {
    selectTab(value) {
      if (value) this.activeTab = value;
    },
  },
};
</script>

<style lang="scss" src="./ProductDetailTabs.scss" scoped/>
