<template>
  <div class="animate-plus-pink2">
    <Background />
    <img
      :src="getImg(image)"
      alt="image"
      class="animate-plus-pink2__image"
    >
    <div class="animate-plus-pink2__text">
      {{ text }}
    </div>
    <div class="animate-plus-pink2__hover-text">
      {{ hoverText }}
    </div>
    <div class="animate-plus-pink2__circle-top">
      <CircleTop />
    </div>
    <div class="animate-plus-pink2__circle-bottom">
      <CircleBottom />
    </div>
  </div>
</template>

<script>
import Background from '@icons/AnimatePlus/pink/bg-svg.svg';
import CircleTop from '@icons/AnimatePlus/pink/circleTop.svg';
import CircleBottom from '@icons/AnimatePlus/pink/circleBottom.svg';
export default {
  name: 'AnimatePlusPink2',
  components: {
    Background,
    CircleTop,
    CircleBottom,
  },
  props:{
    image:{
      type:String,
      default:'',
    },
    text:{
      type: String,
      default: '',
    },
    hoverText:{
      type: String,
      default: '',
    },

  },
  methods:{
    getImg(image) {
      return require(`@images/AnimatePlus/${image}`);
    },
  },

};
</script>

<style lang="scss" src="./AnimatePlusPink2.scss" scoped>

</style>
