<template>
  <button
    class="corner-arrow-button"
    :class="classes"
    @click="$emit('click')"
  >
    <CornerArrowIcon />
  </button>
</template>

<script>
import CornerArrowIcon from '@icons/UiComponent/UiButton/CornerArrowIcon.svg';

export default {
  name: 'CornerArrowButton',
  components:{
    CornerArrowIcon,
  },
  props:{
    up:{
      type: Boolean,
      default: false,
    },
    right:{
      type: Boolean,
      default: false,
    },
    down:{
      type: Boolean,
      default: false,
    },
    disabled:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    classes(){
      return {
        'corner-arrow-button--up':this.up,
        'corner-arrow-button--right':this.right,
        'corner-arrow-button--down':this.down,
        'corner-arrow-button--disabled':this.disabled,
      };
    },
  },
};
</script>

<style lang="scss" src="./CornerArrowButton.scss" scoped/>