<template>
  <SwiperSlide>
    <div
      class="main-slide"
    >
      <div class="main-slide__left">
        <h1>
          {{ title }}
          <slot name="title" />
        </h1>
        <p>{{ text }}</p>
        <div class="main-slide__actions">
          <slot />
        </div>
      </div>
      <div class="main-slide__right">
        <picture class="main-slide__background">
          <source
            v-if="wide"
            :srcset="getImage(`${background}-wide`)"
            media="(min-width: 1921px)"
          >
          <img
            :src="getImage(background)"
            alt="background-1"
          >
        </picture>
        <img
          class="main-slide__pack"
          :src="getImage(pack)"
          alt="pack-1"
        >
      </div>
    </div>
  </SwiperSlide>
</template>

<script>
import { SwiperSlide } from 'vue-awesome-swiper';
export default {
  name: 'MainSlide',
  components:{
    SwiperSlide, 
  },
  props:{
    background:{
      type:String,
      default:() => '',
    },
    pack:{
      type:String,
      default:() => '',
    },
    title:{
      type:String,
      default:() => '',
    },
    text:{
      type:String,
      default:() => '',
    },
    wide:{
      type:Boolean,
      default:()=>false,
    },
  },
  methods:{
    getImage(image){
      return require(`@images/MainSlider/${image}.png`);
    },
  },
};
</script>

<style lang="scss" src="./MainSlide.scss" scoped/>
