<template>
  <div class="ingredient-card">
    <div class="ingredient-card__img">
      <img
        :src="getImg(img)"
        alt="Состав"
      >
    </div>
    <div class="ingredient-card__title">
      <slot />
    </div>
  </div>
</template>
<script>
export default {
  name: 'IngredientCard',
  props:{
    img:{
      type:String,
      default:'',
    },
  },
  methods:{
    getImg(image){
      return require(`@images/IngredientCard/${image}`);
    },
  },
};
</script>

<style lang="scss" src="./IngredientCard.scss"  scoped/>
