<template>
  <button
    class="ui-button simple-arrow-button"
    :class="nextBtnClasses"
    @click="$emit('click')"
  >
    <SimpleArrowIcon />
  </button>
</template>

<script>
import SimpleArrowIcon from '@icons/UiComponent/UiButton/SimpleArrowIcon.svg';


export default {
  name: 'SimpleArrowButton',
  components:{
    SimpleArrowIcon, 
  },
  props:{
    nextBtn:{
      type: Boolean,
      default: false,
    },
    white:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    nextBtnClasses(){
      return {
        'next-button':this.nextBtn,
        'simple-arrow-button--white':this.white,
      };
    },
  },
};
</script>
<style lang="scss" scoped>

@import "../../UiComponents/UiButton/UIButton";
@import "./SimpleArrowButton";

</style>

