<template>
  <header
    class="app-header"
  >
    <div
      ref="header-top"
      class="header-top"
    >
      <ContainerTemplate>
        <div class="header-top__inner">
          <router-link
            to="/"
            class="header-top__logo"
          >
            <span class="header-top__logo-inner--desktop">

              <LogoSlogan class="header-top__logo-slogan" />
              <Logo />
            </span>
            <span class="header-top__logo-inner--mobile">
              <LogoMobile />
            </span>
          </router-link>
          <div class="header-top__menu">
            <SearchInput
              v-model="searchStr"
              :search-hints="searchHints"
              placeholder="Поиск товара"
              active-placeholder="Введите первые буквы"
            />
            <LikeCheckbox v-model="isFavoriteModalShow" />
          </div>
        </div>
      </ContainerTemplate>
    </div>
    <div
      class="header-bottom"
      :class="headerClasses"
    >
      <ContainerTemplate>
        <nav
          class="header-bottom__nav"
          :class="{'header-bottom__nav--search':isShowSearch}"
        >
          <ul>
            <li>
              <router-link to="/">
                <LogoSmall />
              </router-link>
            </li>
            <li>
              <a @click="openCatalog">
                <CatalogIcon />
                Каталог товаров
              </a>
              <CatalogModal v-model="isModalShow" />
            </li>
            <template v-if="!isShowSearch || !isHeaderFixed">
              <li
                v-for="(link,index) in navLinks"
                :key="index"
              >
                <router-link :to="link.href">
                  <MapIcon v-if="link.mapIcon" />
                  {{ link.name }}
                </router-link>
              </li>
            </template>
          </ul>
          <div class="header-bottom__actions">
            <HiddenSearchInput v-model="isShowSearch">
              <SearchInput
                v-model="searchStr"
                :search-hints="searchHints"
                placeholder="Поиск товара"
                active-placeholder="Введите первые буквы"
              />
            </HiddenSearchInput>

            <LikeCheckbox
              v-if="!isShowSearch"
              v-model="isFavoriteModalShow"
            />
          </div>
        </nav>
      </ContainerTemplate>
      <div
        class="header-bottom__shadow"
        @click="isModalShow = !isModalShow"
      />
    </div>
    <div
      ref="header-bottom-mobile"
      class="header-bottom-mobile"
    >
      <div class="header-bottom-mobile__inner">
        <UiLink
          to="/"
          class="header-bottom-mobile__home"
        >
          <HomeIconMobile />
        </UiLink>
        <div class="header-bottom-mobile__actions">
          <UiButton
            to="/catalog"
            class="header-bottom-mobile__button"
            header
          >
            <CatalogIcon />
            Каталог
          </UiButton>
          <UiButton
            to="/catalog"
            class="header-bottom-mobile__button"
            header
          >
            <MapIcon />
            Где купить
          </UiButton>
        </div>
        <button class="header-bottom-mobile__menu">
          <BurgerIcon @click="isMobileModalShow = !isMobileModalShow" />
        </button>
      </div>
    </div>
    <HeaderMobilePopup v-model="isMobileModalShow" />
    <PopupRight
      v-model="isFavoriteModalShow"
      :mobile-modal="isMobileModalShow"
    >
      <FavoriteModal />
    </PopupRight>
  </header>
</template>
<script>
import LogoSlogan from '@icons/AppHeader/logo-slogan.svg';
import Logo from '@icons/AppHeader/logo.svg';
import LogoMobile from '@icons/AppHeader/logo-mobile.svg';
import LogoSmall from '@icons/AppHeader/logo-small.svg';
import MapIcon from '@icons/AppHeader/map-icon.svg';
import CatalogIcon from '@icons/AppHeader/catalog-icon.svg';
import SearchInput from '@components/SearchInput/SearchInput';
import LikeCheckbox from '@components/LikeCheckbox/LikeCheckbox';
import ContainerTemplate from '@templates/ContainerTemplate/ContainerTemplate';
import CatalogModal from '@components/CatalogModal/CatalogModal';
import PopupRight from '@components/PopupRight/PopupRight';
import FavoriteModal from '@components/FavoriteModal/FavoriteModal';
import HiddenSearchInput from '@components/HiddenSearchInput/HiddenSearchInput';
import UiButton from '@ui/UiButton/UiButton';
import UiLink from '@ui/UiLink/UiLink';
import HomeIconMobile from '@icons/UiComponent/UiLink/HomeIconMobile.svg';
import BurgerIcon from '@icons/AppHeader/burger.svg';
import HeaderMobilePopup from '@components/AppHeader/HeaderMobilePopup/HeaderMobilePopup';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    HeaderMobilePopup,
    UiLink,
    UiButton,
    HiddenSearchInput,
    FavoriteModal,
    PopupRight,
    CatalogModal,
    ContainerTemplate,
    LikeCheckbox,
    SearchInput,
    LogoSlogan,
    Logo,
    LogoMobile,
    LogoSmall,
    MapIcon,
    CatalogIcon,
    HomeIconMobile,
    BurgerIcon,
  },
  data() {
    return {
      searchHints: [],
      isShowSearch: false,
      isModalShow: false,
      isMobileModalShow: false,
      isHeaderFixed: false,
      headerOffset: 0,
      isAddLaptopListeners: false,
      isAddListeners: false,
    };
  },
  computed: {
    ...mapGetters([ 'navLinks', 'searchQuery', 'isFavoriteShow' ]),
    searchStr: {
      get() {
        return this.searchQuery;
      },
      set(val) {
        this.setSearchQuery(val);
      },
    },
    isFavoriteModalShow: {
      get() {
        return this.isFavoriteShow;
      },
      set(val) {
        this.setIsFavoriteShow(val);
      },
    },
    headerClasses() {
      return {
        'header-bottom--fixed': this.isHeaderFixed && this.isAddListeners,
        'header-bottom--modal': this.isModalShow,
      };
    },
  },
  watch:{
    ['$route'](){
      this.isModalShow = false;
    },
  },
  mounted() {
    window.addEventListener('resize', this.checkWindowSize);
    this.checkWindowSize();
  },
  destroyed() {
    if (this.isAddListeners) {
      this.removeListeners();
    }
  },
  methods: {
    ...mapActions([ 'setSearchQuery', 'setIsFavoriteShow' ]),
    openCatalog() {
      if (this.isModalShow) return;
      this.isModalShow = !this.isModalShow;
    },
    setOffset() {
      this.headerOffset = this.$refs['header-top'].offsetHeight;
    },
    setBodyPadding(isReset) {
      if (isReset === 'reset') {
        document.body.style.paddingTop = '0px';
        document.body.style.paddingBottom = '0px';
        return;
      }
      document.body.style.paddingTop = this.$refs['header-top'].offsetHeight + 'px';
      document.body.style.paddingBottom = this.$refs['header-bottom-mobile'].offsetHeight + 'px';
    },
    checkScroll() {
      if (window.pageYOffset >= this.headerOffset) this.isHeaderFixed = true;
      else this.isHeaderFixed = false;
    },
    addLaptopListeners() {
      window.addEventListener('resize', this.setBodyPadding);
      this.setBodyPadding();
      this.isAddLaptopListeners = true;
    },
    removeLaptopListeners() {
      window.removeEventListener('resize', this.setBodyPadding);
      this.isAddLaptopListeners = false;
    },
    addListeners() {
      window.addEventListener('scroll', this.checkScroll);
      window.addEventListener('resize', this.setOffset);
      this.setBodyPadding('reset');
      this.isAddListeners = true;
    },
    removeListeners() {
      window.removeEventListener('scroll', this.checkScroll);
      window.removeEventListener('resize', this.setOffset);
      this.isAddListeners = false;
    },
    checkWindowSize() {
      if (window.innerWidth > 1279) {
        if (!this.isAddListeners) {
          this.setOffset();
          this.addListeners();
        }
        if (this.isAddLaptopListeners) {
          this.removeLaptopListeners();
        }
      }
      if (window.innerWidth < 1280) {
        if (this.isAddListeners) this.removeListeners();
        if (!this.isAddLaptopListeners) this.addLaptopListeners();
      }
    },
  },

};
</script>
<style lang="scss" src="./AppHeader.scss" scoped/>
