<template>
  <div class="previews-slider">
    <BlockHeading
      class="previews-slider__title"
      title="Фотогалерея"
    />
    <Swiper
      ref="swiperTop"
      :options="swiperOptions"
    >
      <swiper-slide
        v-for="(image, index) of images"
        :key="index"
        class="previews-slider__slide"
      >
        <PopupGallery
          v-if="openPopup && openId === image.id"
          :gallery="images"
          :active-index="index"
          @closePopup="openPopup = !openPopup"
        />
        <div class="previews-slider__img">
          <UiButton
            class="ui-button--circle ui-button--zoom previews-slider__zoom-btn"
            text="увеличить"
            @click="addPopup(image.id)"
          >
            <ZoomIcon />
            <div class="ui-button__background-zoom" />
          </UiButton>
          <img
            :src="getImg(image.content.image)"
            alt="photo"
            @click="() => $refs.swiperThumbs.$swiper.slideTo(index)"
          >
        </div>
      </swiper-slide>
    </Swiper>
    <div class="previews-slider__nav">
      <Swiper
        ref="swiperThumbs"
        class="previews-slider__pagination"
        :options="swiperOptionThumbs"
      >
        <swiper-slide
          v-for="(image, index) of images"
          :key="index"
          class="previews-slider__pagination-slide"
        >
          <img
            :src="getImg(image.content.image)"
            alt="preview"
            @click="() => $refs.swiperTop.$swiper.slideTo(index)"
          >
        </swiper-slide>
      </Swiper>
      <div class="previews-slider__navigation">
        <SquareArrowButton
          class="previews-slider__prev-btn"
        />
        <SquareArrowButton
          right
          class="previews-slider__next-btn"
        />
      </div>
    </div>
  </div>
</template>

<script>
import PopupGallery from '@components/PopupGallery/PopupGallery';
import ZoomIcon from '@icons/UiComponent/UiButton/ZoomIcon.svg';
import UiButton from '@ui/UiButton/UiButton';
import BlockHeading from '@components/BlockHeading/BlockHeading';
import 'swiper/css/swiper.min.css';
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import SquareArrowButton from '@components/SquareArrowButton/SquareArrowButton';
export default {
  name: 'PreviewsSlider',
  components: {
    SquareArrowButton,
    Swiper,
    SwiperSlide,
    BlockHeading,
    UiButton,
    ZoomIcon,
    PopupGallery,
  },

  data(){
    return{
      swiperOptions: {
        loop: false,
        slidesPerView: 1,
        navigation: {
          nextEl: '.previews-slider__next-btn',
          prevEl: '.previews-slider__prev-btn',
        },
      },
      swiperOptionThumbs:{
        slidesPerView: 'auto',
        touchRatio: 0.2,
        slideToClickedSlide: true,
      },
      // images:[
      //   'photo1.jpg', 'photo2.jpg', 'photo3.jpg', 'photo1.jpg', 'photo2.jpg', 'photo3.jpg',
      // ],
      images:[
        {
          id:1,
          type: 'image',
          content: {
            image: 'PreviewsSlider/photo1.jpg',
            info: 'Название фото, описание краткое.',
          },
        },
        {
          id:2,
          type: 'image',
          content: {
            image: 'PreviewsSlider/photo2.jpg',
            info: 'Название фото, описание краткое.',
          },
        },
        {
          id:3,
          type: 'image',
          content: {
            image: 'PreviewsSlider/photo3.jpg',
            info: 'Название фото, описание краткое.',
          },
        },
        {
          id:4,
          type: 'image',
          content: {
            image: 'PreviewsSlider/photo1.jpg',
            info: 'Название фото, описание краткое.',
          },
        },
        {
          id:5,
          type: 'image',
          content: {
            image: 'PreviewsSlider/photo2.jpg',
            info: 'Название фото, описание краткое.',
          },
        },
        {
          id: 6,
          type: 'image',
          content: {
            image: 'PreviewsSlider/photo3.jpg',
            info: 'Название фото, описание краткое.',
          },
        },
      ],
      openPopup: false,
      openId: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.$swiper;
      const swiperThumbs = this.$refs.swiperThumbs.$swiper;
      if (swiperTop && swiperThumbs){
        swiperTop.controller.control = swiperThumbs;
        swiperThumbs.controller.control = swiperTop;
      }
    });
  },
  methods:{
    getImg(image) {
      return require(`@images/${image}`);
    },
    addPopup(id){
      this.openPopup = !this.openPopup;
      this.openId = id;
      document.documentElement.style.overflow = 'hidden';
    },
    closePopup() {
      this.openPopup = false;
      this.openId = null;
      document.documentElement.style.overflow = 'auto';
    },
  },

};
</script>

<style lang="scss" src="./PreviewsSlider.scss" scoped>

</style>
