<template>
  <component
    :is="component"
    :to="to"
    :href="href"
    class="ui-link"
    :class="classes"
  >
    <slot />
  </component>
</template>

<script>

export default {
  name: 'UiLink',
  props:{
    to:{
      type:String,
      default: () => '',
    },
    href:{
      type:String,
      default: () => '#',
    },
    white:{
      type: Boolean,
      default: false,
    },

  },
  computed:{
    component(){
      if (this.to) return 'router-link';
      return 'a';
    },
    classes() {
      return  {
        'ui-link--white':this.white,
      };
    },
  },

};
</script>

<style lang="scss" src="./UiLink.scss" scoped>

</style>
