<template>
  <div
    class="accordion-item"
    :class="{'accordion-item--active':isActive}"
  >
    <a
      class="accordion-item__toggle"
      @click="$emit('accordionOpen')"
    >
      <div class="accordion-item__title">
        {{ title }}
      </div>
      <div class="accordion-item__toggle-icon" />
    </a>
    <div
      ref="content"
      class="accordion-item__content"
    >
      <div class="accordion-item__content-inner">
        <slot />
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'AccordionItem',
  props:{
    title:{
      type: String,
      default:()=>'',
    },
  },
  data(){
    return {
      isActive:false,
    };
  },
  methods:{
    accordionOpen(timeline){
      this.isActive=true;
      timeline.to(this.$refs.content, {
        height:'auto',
        duration: 0.5,
      });
    },
    accordionClose(timeline){
      this.isActive=false;
      timeline.to(this.$refs.content, {
        height:0,
        duration: 0.5,
      });

    },
  },
};
</script>

<style lang="scss" src="./AccordionItem.scss" scoped/>