<template>
  <div class="block-heading">
    <UiSuptitle
      v-if="isSuptitle && suptitle"
      class="block-heading__suptitle"
    >
      {{ suptitle }}
    </UiSuptitle>
    <h2
      v-if="isTitle"
      class="block-heading__title"
    >
      {{ title }}
    </h2>
  </div>
</template>

<script>
import UiSuptitle from '@ui/UiSuptitle/UiSuptitle';
export default {
  name: 'BlockHeading',
  components: {
    UiSuptitle,
  },
  props:{
    title:{
      type:String,
      default:()=>'',
    },
    suptitle:{
      type:String,
      default:()=>'',
    },
    isTitle:{
      type:Boolean,
      default:() => true,
    },
    isSuptitle:{
      type:Boolean,
      default:() => true,
    },
  },
};
</script>

<style lang="scss" src="./BlockHeading.scss" scoped/>
