<template>
  <component
    :is="component"
    class="ui-button"
    :class="classes"
    :to="to"
    :href="href"
    @click="$emit('click')"
  >
    <slot />
    <div
      v-if="text"
      class="ui-button__text"
    >
      {{ text }}
    </div>
    <div
      v-if="title"
      class="ui-button__title"
    >
      {{ title }}
    </div>
    <div
      v-if="subtitle"
      class="ui-button__subtitle"
    >
      {{ subtitle }}
    </div>
  </component>
</template>

<script>
export default {
  name: 'UiButton',
  props:{
    text:{
      type: String,
      default: '',
    },
    title:{
      type: String,
      default: '',
    },
    subtitle:{
      type: String,
      default: '',
    },
    href:{
      type:String,
      default:() => '',
    },
    to:{
      type:String,
      default:() => '',
    },
    lightBlue:{
      type: Boolean,
      default: false,
    },
    transparent:{
      type: Boolean,
      default: false,
    },
    pink:{
      type: Boolean,
      default: false,
    },
    menu:{
      type: Boolean,
      default: false,
    },
    header:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    component () {
      if (this.to) return 'router-link';
      if (this.href) return 'a';
      return 'button';
    },
    classes() {
      return  {
        'ui-button--lightBlue':this.lightBlue,
        'ui-button--transparent':this.transparent,
        'ui-button--pink':this.pink,
        'ui-button--header':this.header,
        'ui-button--menu':this.menu,
      };
    },
  },
};
</script>
<style lang="scss" src="./UIButton.scss" scoped/>

