<template>
  <teleport to="body">
    <div
      class="popup-step"
      :class="classes"
      @click="$emit('close')"
    >
      <div class="popup-step__inner">
        <CloseButton
          square
          class="popup-step__close-btn"
          @click="$emit('close')"
        />
        <div class="popup-step__title">
          {{ title }}
        </div>
        <div class="popup-step__text">
          {{ text }}
        </div>
        <!-- eslint-disable vue/no-v-html -->
        <div
          class="popup-step__icon"
          v-html="icon"
        />
        <!-- eslint-enable -->
      </div>
    </div>
  </teleport>
</template>

<script>
import teleport from 'vue2-teleport';
import CloseButton from '@components/Buttons/CloseButton/CloseButton';
export default {
  name: 'PopupStep',
  components:{
    teleport,
    CloseButton,
  },
  props:{
    title:{
      type:String,
      default:'',
    },
    text:{
      type:String,
      default:'',
    },
    icon:{
      type:String,
      default:'',
    },
    openPopup:{
      type: Boolean,
      default: false,
    },
  },
  computed:{
    classes(){
      return {
        'popup-step--active':this.openPopup,
      };
    },
  },
};
</script>

<style lang="scss" src="./PopupStep.scss" scoped>

</style>
