<!--Анимация блока производства1-->
<template>
  <div
    ref="image"
    class="animate-image1"
    @mouseover="rotateImg"
    @mouseout="returnImg"
  >
    <div class="animate-image1__img">
      <ConvertImage
        :image="image"
        :kind="kind"
      />
    </div>
    <div class="animate-image1__circle-top">
      <CircleTop />
    </div>
    <div class="animate-image1__circle-bottom">
      <CircleBottom />
    </div>
    <div class="animate-image1__bg-svg">
      <Type1 />
    </div>
  </div>
</template>

<script>
import ConvertImage from '@components/ConvertImage/ConvertImage';
import Type1 from '@icons/AnimateImage/type1/type1.svg';
import CircleTop from '@icons/AnimateImage/type1/circleTop.svg';
import CircleBottom from '@icons/AnimateImage/type1/circleBottom.svg';
export default {
  name: 'AnimateImage1',
  components:{
    Type1,
    CircleTop,
    CircleBottom,
    ConvertImage,
  },
  props: {
    image: {
      type: String,
      default: '',
    },
    kind:{
      type: [ Number, String ],
      default: 1,
    },
  },
  methods:{
    rotateImg(){
      let image = this.$refs.image.querySelector('image');
      image.style.transform = 'rotate(22deg) translate(73px, -143px) scaleY(1.1)';
    },
    returnImg(){
      let image = this.$refs.image.querySelector('image');
      image.style.transform = 'inherit';
    },
  },
};
</script>

<style lang="scss" src="./AnimateImage1.scss" scoped>

</style>
