<template>
  <div
    class="bread-crumbs"
    :class="{'bread-crumbs--active':active}"
  >
    <router-link
      to="/public"
      class="bread-crumbs__link"
    >
      <template v-if="active">
        Главная
      </template>
      <HomeIcon v-else />
    </router-link>
    <template
      v-for="(link,index) in links"
    >
      <div
        :key="'breadcrumbs'+index"
        class="bread-crumbs__split"
      >
        <Arrow v-if="active" />
        <template v-else>
          /
        </template>
      </div>
      <router-link
        :key="index"
        :to="link.href"
        class="bread-crumbs__link"
      >
        {{ link.name }}
      </router-link>
    </template>
  </div>
</template>

<script>
import HomeIcon from '@icons/BreadCrumbs/HomeIcon.svg';
import Arrow from '@icons/BreadCrumbs/arrow.svg';
export default {
  name: 'BreadCrumbs',
  components:{
    HomeIcon,
    Arrow,
  },
  props:{
    links:{
      type:Array,
      default:() => [],
    },
    active:{
      type: Boolean,
      default:() => false,
    },
  },
};
</script>
<style lang="scss" src="./BreadCrumbs.scss" scoped/>

