<template>
  <div class="accordion-list">
    <slot />
  </div>
</template>

<script>
import gsap from 'gsap';

export default {
  name: 'AccordionList',
  data() {
    return {
      timeline: gsap.timeline(),
      activeIndex: -1,
    };
  },
  watch: {
    activeIndex(after, before) {
      this.changeIndexHandler(after, before);
    },
  },
  mounted() {
    if (this.$children.length) this.activeIndex=0;
    this.$children?.forEach((child, i) => {
      child.$on('accordionOpen', () => {
        if (this.activeIndex !== i) this.activeIndex = i;
      });
    });
  },
  methods: {
    changeIndexHandler(after, before=-1) {
      if (before>-1) this.$children[before].accordionClose(this.timeline);
      this.$children[after].accordionOpen(this.timeline);
    },
  },
};
</script>

<style lang="scss" src="./AccordionList.scss" scoped/>