<script>
import Shadow1 from '@icons/ConvertImage/shadow1.svg';
export default {
  name: 'ConvertImage',
  components:{
    Shadow1,
  },
  props: {
    image: {
      type: String,
      default: '',
    },
    kind:{
      type: [ Number, String ],
      default: 1,
    },
  },
  data(){
    return{
      convertedImg:'',
    };
  },
  mounted() {
    this.fetchImg();
  },

  methods:{
    getImg(image) {
      return require(`@images/ConvertImage/${image}`);
    },
    fetchImg(){
      fetch(this.getImg(this.image))
        .then(response => response.blob())
        .then(blob => new Promise((resolve, reject) => {
          const reader = new FileReader();
          reader.onloadend = () => resolve(reader.result);
          reader.onerror = reject;
          reader.readAsDataURL(blob);
        }))
        .then(dataUrl => {
          this.convertedImg = dataUrl;
        });
    },
  },

};
</script>

<style lang="scss" src="./ConvertImage.scss" scoped>

