<template>
  <fragment>
    <PopupGallery
      v-if="openPopup"
      :gallery="[image]"
      @closePopup="openPopup = !openPopup"
    />
    <div class="article-image">
      <div class="article-image__name">
        Пример картинки в тексте. Подпись к картинке
      </div>
      <div class="article-image__image-container">
        <img
          :src="getImg(image.content.image)"
          alt="image"
        >
        <UiButton
          class="ui-button--circle ui-button--zoom article-image__zoom"
          text="увеличить"
          @click="addPopup"
        >
          <ZoomIcon />
          <div class="ui-button__background-zoom" />
        </UiButton>
      </div>
    </div>
  </fragment>
</template>

<script>
import PopupGallery from '@components/PopupGallery/PopupGallery';
import UiButton from '@ui/UiButton/UiButton';
import ZoomIcon from '@icons/UiComponent/UiButton/ZoomIcon.svg';
export default {
  name: 'ArticleImage',
  components:{
    UiButton,
    ZoomIcon,
    PopupGallery,
  },
  props:{
    image:{
      type:Object,
      default:()=>({}),
    },
  },
  data(){
    return{
      openPopup: false,
    };
  },
  methods:{
    getImg(image){
      return require(`@images/${image}`);
    },
    addPopup(){
      this.openPopup = !this.openPopup;
      document.documentElement.style.overflow = 'hidden';
    },
  },

};
</script>

<style lang="scss" src="./ArticleImage.scss" scoped>

</style>
